import {
  SHOW_MESSAGE,
  NOTIFICATION_ERROR,

  GET_PACKAGE,
  GET_ONE_PACKAGE,
  ADD_PACKAGE,
  EDIT_PACKAGE,

  UI_START_ACTION,
  UI_STOP_ACTION,
} from "../constants/ActionTypes";

import axios from 'util/Api'

export const getPackageByService = (service_id) => {
  return dispatch => {
    dispatch({type: UI_START_ACTION, name: GET_PACKAGE});
    axios.get(`package/service/${service_id}/all`).then(({data}) => {
      dispatch({type: UI_STOP_ACTION, name: GET_PACKAGE});
      dispatch({type: GET_PACKAGE, payload: data.data});
    }).catch(({response}) => {
      dispatch({type: UI_STOP_ACTION, name: GET_PACKAGE});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'Package error.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};

