import React from 'react';
import {connect} from 'react-redux';
import moment from "moment";
import _ from 'lodash';

import {isLoading} from 'store/selectors';
import {getOneMarketService} from 'actions/Service';
import {addTrialSubscription} from 'actions/Subscription';
import {onResetStatus, onResetDataState} from 'actions/Common';
import {GET_ONE_SERVICE, ADD_SUBSCRIPTION} from "constants/ActionTypes";
import PackageCard from "components/PackageCard";

import {Icon} from '@ant-design/compatible';
import {
  CheckCircleFilled,
  ExclamationCircleOutlined,
  InfoCircleOutlined,
  BookOutlined,
  AppstoreOutlined
} from '@ant-design/icons';
import {
  Space,
  Typography,
  Button,
  Row, Col,
  Spin,
  Empty,
  Modal,
  Card,
  Avatar,
  Tabs
} from 'antd';

class Detail extends React.Component {

  componentDidMount() {
    const { match : { params } } = this.props;
    if (params.id) {this.props.getOneMarketService(params.id)}
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.created && this.props.created) {
      const {match: {params}} = this.props;
      if (params.id) {this.props.getOneMarketService(params.id)}
      this.props.onResetStatus()
    }
  }

  componentWillUnmount() {this.props.onResetDataState()}

  onGetTrial = () => {
    const { match : { params }, addTrialSubscription } = this.props;
    if (params.id)
      Modal.confirm({
        title: 'Do you want to continue trial?',
        icon: <ExclamationCircleOutlined/>,
        content: 'Accepting trial will give you one-time 1 month trial service.',
        onOk() {
          addTrialSubscription(params.id)
        }
      });
  };

  render() {
    const {service, loading} = this.props;
    
    const {Docs, Overview} = service && service.info ? require(`../../../${service.info}`) : {};

    return(
      <Space direction={'vertical'} style={{width: '100%'}}>
        <Typography.Title level={4}>API Market</Typography.Title>
        <Card bordered={false}>
          <Spin spinning={loading.get}>
            {service ?
            <Row justify="center" gutter={[24]} style={{minHeight: 150, margin: '24px 0'}}>
              <Col span={4}>
                <Avatar size={100} style={{color: '#f56a00', backgroundColor: '#fde3cf', float: 'right'}}
                        icon={<Icon type={service.icon}/>} />
              </Col>
              <Col span={16}>
                <Space direction={'vertical'} style={{width: '100%'}}>
                  <div>
                    <Typography.Title level={3} style={{marginBottom: 0}}>{service.name}</Typography.Title>
                    <Typography.Text type={'secondary'}>{service.service_group.name}</Typography.Text>
                  </div>
                  <Typography.Paragraph style={{marginTop: '1em'}}>{service.description}</Typography.Paragraph>
                  {service.package ? <>
                    {service.subscription ?
                      <Typography.Text><Typography.Text type="success"><CheckCircleFilled/></Typography.Text> API
                        enabled since {moment(service.subscription.start_date).format('LL')}</Typography.Text> :
                      <Button type="primary" onClick={this.onGetTrial}>Get trial</Button>}
                  </> : <Typography.Text><Typography.Text type="warning"><ExclamationCircleOutlined/></Typography.Text> No 
                    trial package provided</Typography.Text>}
                </Space>
              </Col>
            </Row> : <Empty style={{minHeight: 150}}/>}
            {service &&
            <Tabs defaultActiveKey="1">
              <Tabs.TabPane
                tab={<span><InfoCircleOutlined/> Overview</span>}
                key="1">
                {Overview ? <Overview/> : <Empty/>}
              </Tabs.TabPane>
              <Tabs.TabPane
                tab={<span><BookOutlined/> Documentation</span>}
                key="2">
                {Docs ? <Docs/> : <Empty/>}
              </Tabs.TabPane>
              <Tabs.TabPane
                tab={<span><AppstoreOutlined/> Packages</span>}
                key="3">
                <Row gutter={[24]}>
                  {_.map(service.packages, (_package) =>
                    (<Col {...{xxl: 6, xl: 8, lg: 12, sm: 24}}><PackageCard package={_package}/></Col>)
                  )}
                </Row>
              </Tabs.TabPane>
            </Tabs>}
          </Spin>
        </Card>
      </Space>
    );
  }

}

const mapStateToProps = (store) => {
  const {service: {service}, subscription: {status: {created}}} = store;
  return {
    loading: {
      get: isLoading(store, GET_ONE_SERVICE),
      add: isLoading(store, ADD_SUBSCRIPTION),
    },
    service, created
  }
};

export default connect(mapStateToProps, {getOneMarketService, addTrialSubscription, onResetStatus, onResetDataState})(Detail);