import React from 'react';
import {connect} from 'react-redux';
import moment from 'moment';

import {isLoading} from 'store/selectors';
import {GET_SUBSCRIPTION_SUMMARY,} from "constants/ActionTypes";
import {getSubscriptionSummary} from 'actions/Subscription';
import {onResetStatus} from 'actions/Common';

import SubscriptionStatusTag from "components/Tags/SubscriptionStatus";
import PackageTag from "components/Tags/Package";
import SubscriptionFrom from '../Form/Subs';

import {
  EditOutlined,
  TagsOutlined,
  AlertOutlined,
} from '@ant-design/icons';
import {
  Spin,
  Button,
  Space,
  Typography,
  Row, Col,
  Statistic,
  Descriptions,
} from 'antd';

class SubscriptionSummary extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      show: null,
      row: null
    }
  }

  componentDidMount() {
    this.props.getSubscriptionSummary(this.props.subs)
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if ((!prevProps.edited && this.props.edited)) {
      this.onHideForm();
      this.props.getSubscriptionSummary(this.props.subs);
      this.props.onResetStatus();
    }
  }

  onShowForm = (row = null) => this.setState({show: true, row});

  onHideForm = () => this.setState({show: null, row: null});

  render() {

    const {loading, summary} = this.props;

    return(
      <Spin spinning={loading}>
        {summary && <>
        <Space size={'middle'} direction={'vertical'} style={{width: '100%'}}>
          <Button type="primary" style={{float: 'right'}} icon={<EditOutlined/>}
                  onClick={() => this.onShowForm(null)}>
            Edit Subscription
          </Button>
          <Row gutter={16}>
            <Col {...{lg: 6, sm: 12}}>
              <Statistic title="Valid Until" value={moment(summary.end_date).format('ll')}/>
              {moment().isSameOrAfter(moment(summary.end_date).subtract(7, 'day')) &&
              <Typography.Text
                type={'danger'}><AlertOutlined/> ends {moment(summary.end_date).add(1, 'day').fromNow()}
              </Typography.Text>}
            </Col>
            <Col {...{lg: 6, sm: 12}}>
              {summary.activeAddOns.length ?
                <Statistic title="Active Add-ons (today)" value={`${summary.activeAddOns.length} add-ons`}
                           prefix={<TagsOutlined/>}/> :
                <Statistic title="Active Add-ons (Today)" value={'No add-on'} prefix={<TagsOutlined/>}/>}
            </Col>
          </Row>
          <Descriptions
            bordered
            size={'middle'}
            column={1}>
            <Descriptions.Item label={<div style={{textAlign: 'right'}}>Subscription Status</div>}>
              <SubscriptionStatusTag status={summary.status}/>
            </Descriptions.Item>
            <Descriptions.Item label={<div style={{textAlign: 'right'}}>Subscription ID</div>}>
              {summary.code} <Typography.Text type={'secondary'}>(generated)</Typography.Text>
            </Descriptions.Item>
            <Descriptions.Item label={<div style={{textAlign: 'right'}}>Service</div>}>
              {summary.service.name} <Typography.Text type={'secondary'}>{summary.service.service_group.name}</Typography.Text>
            </Descriptions.Item>
            <Descriptions.Item label={<div style={{textAlign: 'right'}}>Package</div>}>
              {<PackageTag name={summary.package.name}/>}
            </Descriptions.Item>
            <Descriptions.Item label={<div style={{textAlign: 'right'}}>Start Subscription</div>}>
              {moment(summary.start_date).format('ll')}
            </Descriptions.Item>
            <Descriptions.Item label={<div style={{textAlign: 'right'}}>End Subscription</div>}>
              {moment(summary.end_date).format('ll')}
            </Descriptions.Item>
            <Descriptions.Item label={<div style={{textAlign: 'right'}}>Cut-off Date</div>}>
              {summary.cut_off_date ? `${summary.cut_off_date} per month` : `lifetime`}
            </Descriptions.Item>
            <Descriptions.Item label={<div style={{textAlign: 'right'}}>Registered Add-on</div>}>
              {summary.addOns.length ? <><TagsOutlined/> {summary.addOns.length} add-ons</> : 'No add-on'}
            </Descriptions.Item>
            <Descriptions.Item label={<div style={{textAlign: 'right'}}>Active Add-on (today)</div>}>
              {summary.activeAddOns.length ? <><TagsOutlined/> {summary.activeAddOns.length} add-ons</> : 'No add-on'}
            </Descriptions.Item>
          </Descriptions>
        </Space>
        <SubscriptionFrom show={this.state.show} onCancel={this.onHideForm} row={summary}/>
        </>}

      </Spin>
    );

  }
}

const mapStateToProps = (store) => {
  const {subscription: {summary, status: {edited}}} = store;
  return {
    loading: isLoading(store, GET_SUBSCRIPTION_SUMMARY),
    summary, edited
  }
};

export default connect(mapStateToProps, {
  getSubscriptionSummary, onResetStatus
})(SubscriptionSummary);