import React from 'react';
import {connect} from 'react-redux';
import {notification} from 'antd';

import {onResetNotification} from '../../actions/Notification'

class Notification extends React.Component {

  componentDidUpdate(prevProps, prevState, snapshot) {
    const {title, message, severity} = this.props;

    if (severity) {
      notification[severity]({
        message: title,
        description: message,
        onClose: () => {
          this.props.onResetNotification()
        }
      });
    }
  }

  render() {
    return null
  }

}

const mapStateToProps = ({ui}) => {
  const {title, message, severity} = ui.notification;
  return {title, message, severity}
};

export default connect(mapStateToProps, {onResetNotification})(Notification)