import React from 'react';
import _ from 'lodash';

import {Tag} from 'antd';

const SubscriptionStatusTag = ({status}) => {
  switch (status) {
    case 'active':
      return <Tag color={'green'}>{_.upperCase(status)}</Tag>;
    case 'expired':
      return <Tag color={'red'}>{_.upperCase(status)}</Tag>;
    case 'inactive':
      return <Tag color={'volcano'}>{_.upperCase(status)}</Tag>;
    case 'incoming':
      return <Tag color={'blue'}>{_.upperCase(status)}</Tag>;
    default:
      return <Tag color={'purple'}>{_.upperCase(status)}</Tag>;
  }
};

export default SubscriptionStatusTag