import {
  GET_ALL_SERVICE,
  GET_ONE_SERVICE,
  GET_SUBSCRIBED_SERVICE,
  RESET_DATA_STATE,
  RESET_STATUS,
  ADD_SERVICE,
  ADD_SERVICE_GROUP,
  EDIT_SERVICE,
  EDIT_SERVICE_GROUP
} from "../constants/ActionTypes";

const INIT_STATE = {
  services: [],
  service: null,
  status: {
    created: null,
    edited: null
  }
};

const Service = (state = INIT_STATE, action) => {
  switch (action.type) {
    case GET_ALL_SERVICE:
      return {...state, services: action.payload};
    case GET_ONE_SERVICE:
      return {...state, service: action.payload};
    case GET_SUBSCRIBED_SERVICE:
      return {...state, services: action.payload};
    case ADD_SERVICE:
      return {...state, status: {...state.status, created: true}};
    case ADD_SERVICE_GROUP:
      return {...state, status: {...state.status, created: true}};
    case EDIT_SERVICE:
      return {...state, status: {...state.status, edited: true}};
    case EDIT_SERVICE_GROUP:
      return {...state, status: {...state.status, edited: true}};
    case RESET_STATUS:
      return {...state, status: INIT_STATE.status};
    case RESET_DATA_STATE:
      return INIT_STATE;
    default:
      return state;
  }
};

export default Service;