import {
  SHOW_MESSAGE,
  NOTIFICATION_ERROR,

  GET_ACCESS_LOG,
  UI_START_ACTION,
  UI_STOP_ACTION,
} from "../constants/ActionTypes";

import axios from 'util/Api'

export const getAccessLog = (service, user) => {
  return dispatch => {
    dispatch({type: UI_START_ACTION, name: GET_ACCESS_LOG});
    axios.get(`log/service/${service}/${user}`).then(({data}) => {
      dispatch({type: UI_STOP_ACTION, name: GET_ACCESS_LOG});
      dispatch({type: GET_ACCESS_LOG, payload: data.data});
    }).catch(({response}) => {
      dispatch({type: UI_STOP_ACTION, name: GET_ACCESS_LOG});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'Log error.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};

