import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';
import moment from "moment";

import {isLoading} from 'store/selectors';
import {GET_SUBSCRIPTION_DAILY_USAGE} from "constants/ActionTypes";
import {getSubscriptionDailyUsage} from 'actions/Subscription';

import {
  TagsOutlined
} from '@ant-design/icons';
import {
  Table, Typography, Spin, Space, Select
} from 'antd';

class SubscriptionDailyUsage extends React.Component {

  componentDidMount() {
    this.props.getSubscriptionDailyUsage(this.props.subs)
  }

  handleDateChange = value => {
    const [start, end] = _.split(value, ';');
    this.props.getSubscriptionDailyUsage(this.props.subs, {start, end});
  };

  render() {

    const {loading, dailyUsage} = this.props;

    const periods = dailyUsage.periods ? dailyUsage.periods : [];

    const columns = dailyUsage.columns ? dailyUsage.columns : [];

    const data = dailyUsage.data ? dailyUsage.data : [];

    const dateStringValue = ([start, end]) => moment(start).format('YYYY-MM-DD') + ';' + moment(end).format('YYYY-MM-DD');

    const dateColumn = [{title: 'Date', dataIndex: 'date', key: 'date', sorter: (a, b) => moment(a.date) - moment(b.date),
      defaultSortOrder: 'descend', render: (date) => date ? moment(date).format('LL') : null}];

    const dataColumn = _.map(columns, ({key, ...column}) => ({...column, key, sorter: (a, b) => a[key] - b[key],
          render: value => value ? <Typography.Text mark>{value}</Typography.Text> : value}));
    
    const current = _.find(periods, ([start, end]) => moment().isBetween(moment(start), moment(end), 'day', '[]')) ? 
      _.find(periods, ([start, end]) => moment().isBetween(moment(start), moment(end), 'day', '[]')) :
      _.last(periods);

    return (
      <Spin spinning={loading}>
        <Space direction={'vertical'} style={{width: '100%'}}>
          {periods.length &&
          <Select
            showSearch
            defaultValue={dateStringValue(current)}
            onChange={this.handleDateChange}
            loading={loading} style={{float: 'right'}}>
            {_.map(periods, ([start, end]) => <Select.Option value={dateStringValue([start, end])}>
              {moment(start).format('DD MMMM YYYY')} - {moment(end).format('DD MMMM YYYY')}
            </Select.Option>)}
          </Select>}
          <Table columns={[...dateColumn, ...dataColumn]} dataSource={data} pagination={false}
                 style={{scrollBehavior: 'smooth'}}
                 summary={(data => (
                   <Table.Summary.Row id={'summary'}>
                     <Table.Summary.Cell><Typography.Text strong>Total Usage</Typography.Text></Table.Summary.Cell>
                     {_.map(columns, ({dataIndex, _limit, _addOn}) => {
                       const addOn = () => _addOn ? <TagsOutlined/> : null;
                       if (_limit === 'unlimited')
                         return (<Table.Summary.Cell>
                           <Typography.Text strong
                                            type="success">{_.sumBy(data, dataIndex)} / {_limit} {addOn()}</Typography.Text>
                         </Table.Summary.Cell>);
                       else if (parseInt(_limit))
                         return (<Table.Summary.Cell>
                           <Typography.Text strong>{_.sumBy(data, dataIndex)} / {_limit} {addOn()}</Typography.Text>
                         </Table.Summary.Cell>);
                       else if (_.isNull(_limit))
                         return (<Table.Summary.Cell>
                           <Typography.Text strong type="danger">Locked</Typography.Text>
                         </Table.Summary.Cell>);
                       else
                         return <Table.Summary.Cell/>;
                     })}
                   </Table.Summary.Row>
                 ))}/>
        </Space>
      </Spin>
    );
  }

}

const mapStateToProps = (store) => {
  const {subscription: {subscription, dailyUsage}} = store;
  return {
    loading: isLoading(store, GET_SUBSCRIPTION_DAILY_USAGE),
    subscription, dailyUsage
  }
};

export default connect(mapStateToProps, {getSubscriptionDailyUsage})(SubscriptionDailyUsage);