import React, {Component} from 'react';
import {Redirect, Route, Switch} from 'react-router-dom';
import {connect} from 'react-redux';
import {IntlProvider} from 'react-intl'
import AppLocale from '../lngProvider';

import {setInitUrl} from '../actions/Auth';

import MainApp from 'app/index';
import Auth from "./Auth";
import Forgot from './Forgot';

import EmailVerification from './Verification/Email';
import PasswordVerification from './Verification/Password';

import Error404 from "components/Error404";
import Error403 from "components/Error403";
import Notification from "components/Notification";

const AuthenticatedRoute = ({component: Component, token, ...rest}) =>
  <Route
    {...rest}
    render={props =>
      token
        ? <Component {...props}/>
        : <Redirect
          to={{
            pathname: '/login',
            state: {from: props.location}
          }}
        />}
  />;

class App extends Component {

  componentWillMount() {
    const {initURL, history, setInitUrl} = this.props;
    if (initURL === '') setInitUrl(history.location.pathname);
  }

  render() {
    const {match, location, locale, token, initURL} = this.props;
    if (location.pathname === '/') {
      if (token === null) {
        return ( <Redirect to={'/login'}/> );
      } else if (initURL === '' || initURL === '/' || initURL === '/login') {
        return ( <Redirect to={'/app'}/> );
      } else {
        return ( <Redirect to={initURL}/> );
      }
    }

    const currentAppLocale = AppLocale[locale.locale];

    return (
      <IntlProvider
        locale={currentAppLocale.locale}
        messages={currentAppLocale.messages}>
        <div className="app-main">
          <Switch>
            <AuthenticatedRoute path={`${match.url}app`} token={token} component={MainApp}/>
            <Route path='/login' component={Auth}/>
            <Route path='/forgot' component={Forgot}/>
            <Route path='/verify/email' component={EmailVerification}/>
            <Route path='/verify/password' component={PasswordVerification}/>
            <Route path='/404' component={Error404}/>
            <Route path='/403' component={Error403}/>
          </Switch>
          <Notification/>
        </div>
      </IntlProvider>
    );
  }

}

const mapStateToProps = ({settings, auth}) => {
  const {locale} = settings;
  const {token, initURL} = auth;
  return {token, locale, initURL}
};

export default connect(mapStateToProps, {setInitUrl})(App);