import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';
import './Quota.css'
import { orange, red, green } from '@ant-design/colors';

import {
  Collapse, Space, Typography, Progress, Row, Col, Statistic, Divider
} from 'antd';

import PackageTag from "components/Tags/Package";

import UsageChart from "./Chart/UsageChart";

class Quota extends React.Component {

  renderLimit = (is_unlimited, limit, reset) => {
    if (is_unlimited)
      return <Statistic title="Limit" value={'Unlimited'} valueStyle={{color: green.primary}}/>;
    else if (_.isNull(limit))
      return <Statistic title="Limit" value={'Locked'} valueStyle={{color: red.primary}}/>;
    else
      return <>
        <Statistic title="Limit" value={limit}/>
        {reset && reset !== 'never' && <Typography.Text type={'secondary'}>{reset}</Typography.Text>}
      </>;
  };
  
  renderMiniProgress = (is_unlimited, limit, usage) => {
    if (is_unlimited)
      return <Space>
        <Typography.Text strong type={'success'}>Unlimited</Typography.Text>
        <div style={{width: 170}}><Progress percent={100}/></div>
      </Space>;
    else if (_.isNull(limit))
      return <Space>
        <Typography.Text strong type={'danger'}>Locked</Typography.Text>
        <div style={{width: 170}}><Progress percent={100} status="exception"/></div>
      </Space>;
    else {
      const percent = usage / limit * 100;
      if (percent >= 100)
        return <Space>
          <Typography.Text strong type={'danger'}>Limit Reached</Typography.Text>
          <div style={{width: 170}}><Progress percent={100} status="exception"/></div>
        </Space>;
      else if (percent >= 95)
        return <div style={{width: 170}}><Progress percent={Math.round(percent)}
                                                   strokeColor={{'0%': red.primary, '100%': red.primary}}/>
        </div>;
      else if (percent >= 80)
        return <div style={{width: 170}}><Progress percent={Math.round(percent)} strokeColor={{
          '0%': orange.primary,
          '100%': orange.primary
        }}/></div>;
      else
        return <div style={{width: 170}}><Progress percent={Math.round(percent)}/></div>
    }
  };

  renderProgress = (is_unlimited, limit, usage) => {
    if (is_unlimited)
      return <Progress type={'circle'} percent={100} format={() => 'Unlimited'}/>;
    else if (_.isNull(limit))
      return <Progress type={'circle'} percent={100} status="exception" format={() => 'Locked'}/>;
    else {
      const percent = usage / limit * 100;
      if (percent >= 100)
        return <Progress type={'circle'} percent={100} status="exception" format={() => 'Reached'}/>;
      else if (percent >= 95)
        return <Progress type={'circle'} percent={Math.round(percent)}
                         strokeColor={{'0%': red.primary, '100%': red.primary}}/>;
      else if (percent >= 80)
        return <Progress type={'circle'} percent={Math.round(percent)}
                         strokeColor={{'0%': orange.primary, '100%': orange.primary}}/>;
      else
        return <Progress type={'circle'} percent={Math.round(percent)}/>
    }
  };
  
  render() {
    const {service, period} = this.props;
    const [start, end] = period;
    
    const limits = _(service.subscriptions).map(({package: _package, ...rest}) => {
      return _.map(_package.limits, (limit) => {
        return {...limit, package: _package.name}
      })
    }).flatten().value();
    
    return (
      <Collapse
        accordion
        defaultActiveKey={['1']}
        bordered={false}
        className="site-collapse-custom-collapse">
        {_.map(limits, ({id, code, limit, reset, is_unlimited, usages, package: _package}, index) => (
          <Collapse.Panel
            header={
              <Space>
                <Typography.Text>{_.capitalize(code)}</Typography.Text>
                <PackageTag name={_package}/>
              </Space>
            }
            extra={this.renderMiniProgress(is_unlimited, limit, _.sumBy(usages, 'amount'))}
            key={(index + 1) + ''}
            className="site-collapse-custom-panel">
            <Row gutter={[16, 24]}>
              <Col span={6}>
                <Row gutter={[16, 24]}>
                  <Col span={11}><Statistic title="Usage" value={_.sumBy(usages, 'amount')}/></Col>
                  <Col span={2}><Divider type="vertical" style={{height: '100%'}}/></Col>
                  <Col span={11}>{this.renderLimit(is_unlimited, limit, reset)}</Col>
                  <Col span={24} style={{padding: '45px 8px'}}>
                    <Space direction="horizontal" style={{width: '100%', justifyContent: 'center'}}>
                      {this.renderProgress(is_unlimited, limit, _.sumBy(usages, 'amount'))}
                    </Space>
                  </Col>
                </Row>
              </Col>
              <Col span={18}>
                <UsageChart identifier={`usage-chart-${index}`} data={usages} start={start} end={end}
                            limit={is_unlimited ? null : limit ? limit : 0}/>
              </Col>
            </Row>
          </Collapse.Panel>
        ))}
      </Collapse>
    );
  }

}

const mapStateToProps = ({usage}) => {
  const {service} = usage;
  return {service}
};

export default connect(mapStateToProps, {})(Quota);