import React from 'react';
import {connect} from 'react-redux';

import {editPassword} from 'actions/User';
import {onResetStatus} from 'actions/Common';
import {isLoading} from 'store/selectors';

import {EDIT_USER} from "constants/ActionTypes";

import {
  Form, Typography, Space, Card, Input, Button
} from 'antd';

const layout = {
  labelCol: {span: 8},
  wrapperCol: {span: 16}
};

const tailLayout = {
  wrapperCol: {offset: 8, span: 16}
};

class Security extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      editMode: false,
      form: React.createRef()
    }
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if ((!prevProps.edited && this.props.edited)) {
      this.state.form.current.resetFields();
      this.setState({editMode: false});
      this.props.onResetStatus();
    }
    if (!prevState.editMode && this.state.editMode) {
      this.state.form.current.resetFields();
    }
  }

  toggleEditMode = (mode) => this.setState({editMode: mode});

  onSubmit = () => {
    const {form} = this.state;
    form.current
      .validateFields()
      .then(({old_password, new_password, ...rest}) => {
        this.props.editPassword({old_password, new_password});
      })
      .catch((info) => console.log('Validate Failed:', info));
  };

  render() {
    const {loading} = this.props;
    const {editMode, form} = this.state;

    return (
      <Space direction={'vertical'} style={{width: '100%'}}>
        <Typography.Title level={5}>Security</Typography.Title>
        <Card bordered={false}>
          {editMode ?
            <Form {...layout} ref={form} name="control-ref" onFinish={this.onSubmit}>
              <Form.Item name="old_password" label="Old Password" hasFeedback rules={[
                {required: true, message: 'Please input password!',},
                {min: 8, message: 'Password must be at least 8 characters!'}
              ]}>
                <Input.Password type="password"/>
              </Form.Item>
              <Form.Item name="new_password" label="New Password" hasFeedback rules={[
                {required: true, message: 'Please input password!',},
                {min: 8, message: 'Password must be at least 8 characters!'}
              ]}>
                <Input.Password type="password"/>
              </Form.Item>
              <Form.Item
                name="confirmPassword"
                label="Confirm Password"
                dependencies={['new_password']}
                hasFeedback
                rules={[
                  {required: true, message: 'Please input password again!'},
                  ({getFieldValue}) => ({
                    validator(rule, value) {
                      if (!value || getFieldValue('new_password') === value) {
                        return Promise.resolve();
                      }
                      return Promise.reject('The two passwords that you entered do not match!');
                    },
                  }),
                ]}>
                <Input.Password type="password"/>
              </Form.Item>
              <Form.Item {...tailLayout}>
                <Space>
                  <Button htmlType="button" onClick={() => this.toggleEditMode(false)}>
                    Cancel
                  </Button>
                  <Button type="primary" htmlType="submit" loading={loading}>
                    Submit
                  </Button>
                </Space>
              </Form.Item>
            </Form>:
            <Typography.Link onClick={() => this.toggleEditMode(true)}>Change Password</Typography.Link>
          }
        </Card>
      </Space>
    );
  }

}

const mapStateToProps = (store) => {
  const {user: {status: {edited}}} = store;
  return {edited, loading: isLoading(store, EDIT_USER)}
};

export default connect(mapStateToProps, {editPassword, onResetStatus})(Security);