import React from 'react';
import _ from 'lodash';

import {Avatar, Space} from 'antd';

const UserAvatar = ({name, only}) => {
  const initial = name.split(' ')
    .map(([first, ...rest]) => first)
    .join('').toUpperCase();

  if (only)
    return <Avatar style={{color: '#f56a00', backgroundColor: '#fde3cf',}}>{initial}</Avatar>;
  else
    return <Space>
      <Avatar style={{color: '#f56a00', backgroundColor: '#fde3cf',}}>{initial}</Avatar>
      {_.startCase(name)}
    </Space>
};

export default UserAvatar