import {
  SWITCH_LANGUAGE, TOGGLE_COLLAPSED_NAV, TOGGLE_SETTING
} from "../constants/ActionTypes";

const init = {
  openSetting: false,
  navCollapsed: true,
  locale: {
    languageId: 'english',
    locale: 'en',
    name: 'English',
    icon: 'us'
  }
};

const settings = (state = init, action) => {
  switch (action.type) {
    case TOGGLE_COLLAPSED_NAV:
      return {...state, navCollapsed: action.isNavCollapsed};
    case SWITCH_LANGUAGE:
      return {...state, locale: action.payload};
    case TOGGLE_SETTING:
      return {...state, openSetting: action.isSettingOpened};
    default:
      return state;
  }
};

export default settings;