import React from 'react';
import {connect} from 'react-redux';

import {editUser} from 'actions/User';
import {getUser} from 'actions/Auth';
import {onResetStatus} from 'actions/Common';
import {isLoading} from 'store/selectors';

import {EDIT_USER} from "constants/ActionTypes";

import {EditOutlined, QuestionCircleOutlined} from '@ant-design/icons';
import {
  Form, Typography, Space, Descriptions, Card, Tooltip, Input, Button
} from 'antd';

const layout = {
  labelCol: {span: 8},
  wrapperCol: {span: 16}
};

const tailLayout = {
  wrapperCol: {offset: 8, span: 16}
};

const noPadding = {
  padding: 0
};

class Profile extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      editMode: false,
      form: React.createRef()
    }
  }
  
  componentDidUpdate(prevProps, prevState, snapshot) {
    if ((!prevProps.edited && this.props.edited)) {
      this.state.form.current.resetFields();
      this.setState({editMode: false});
      this.props.getUser();
      this.props.onResetStatus();
    }
    if (!prevState.editMode && this.state.editMode) {
      const {mysights_id, fullname, username, email} = this.props.authUser;
      this.state.form.current.setFieldsValue({mysights_id, fullname, username, email});
    }
  }

  toggleEditMode = (mode) => this.setState({editMode: mode});
  
  onSubmit = () => {
    const {form} = this.state;
    form.current
      .validateFields()
      .then(({fullname, email, ...rest}) => {
        this.props.editUser({fullname, email});
      })
      .catch((info) => console.log('Validate Failed:', info));
  };
  
  render() {

    const {authUser, loading} = this.props;
    const {editMode, form} = this.state;
    const {mysights_id, fullname, username, email, change_email} = authUser;
    
    return (
      <Space direction={'vertical'} style={{width: '100%'}}>
        <Typography.Title level={5}>
          Profile <Tooltip title={'Edit'}><Typography.Link><EditOutlined
          onClick={() => this.toggleEditMode(!editMode)}/></Typography.Link></Tooltip>
        </Typography.Title>
        <Card bordered={false} bodyStyle={editMode ? {} : noPadding}>
          {editMode ?
            <Form {...layout} ref={form} name="control-ref" onFinish={this.onSubmit}>
              <Form.Item name="mysights_id" label="Mysights-ID">
                <Input disabled/>
              </Form.Item>
              <Form.Item name="fullname" label="Fullname" rules={[
                {required: true, message: 'Please input fullname!'}
              ]}>
                <Input/>
              </Form.Item>
              <Form.Item name="username" label="Username">
                <Input disabled/>
              </Form.Item>
              <Form.Item name="email" label="Email" rules={[
                {type: 'email', message: 'The input is not valid email!'},
                {required: true, message: 'Please input email!'}
              ]}>
                <Input/>
              </Form.Item>
              <Form.Item {...tailLayout}>
                <Space>
                  <Button htmlType="button" onClick={() => this.toggleEditMode(false)}>
                    Cancel
                  </Button>
                  <Button type="primary" htmlType="submit" loading={loading}>
                    Submit
                  </Button>
                </Space>
              </Form.Item>
            </Form> :
            <Descriptions column={1} bordered>
              <Descriptions.Item
                label={<Typography.Text strong>Mysights-ID</Typography.Text>}>{mysights_id}</Descriptions.Item>
              <Descriptions.Item
                label={<Typography.Text strong>Fullname</Typography.Text>}>{fullname}</Descriptions.Item>
              <Descriptions.Item
                label={<Typography.Text strong>Username</Typography.Text>}>{username}</Descriptions.Item>
              <Descriptions.Item label={<Typography.Text strong>Email</Typography.Text>}>
                <Space>
                  {email}
                  {change_email &&
                  <Tooltip title={`Email changed to ${change_email}, please verify your email!`}>
                    <Typography.Text type={'warning'}><QuestionCircleOutlined/></Typography.Text>
                  </Tooltip>}
                </Space>
              </Descriptions.Item>
            </Descriptions>
          }
        </Card>
      </Space>
    );
  }

}

const mapStateToProps = (store) => {
  const {auth: {authUser}, user: {status: {edited}}} = store;
  return {authUser, edited, loading: isLoading(store, EDIT_USER)}
};

export default connect(mapStateToProps, {editUser, getUser, onResetStatus})(Profile);