import React from 'react';
import {Route, Switch, Redirect, withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import {Layout, Spin, Alert, Typography} from 'antd';
import './index.css';
import axios from 'util/Api';

import Sidebar from 'containers/Sidebar/index';
import Navbar from 'containers/Header/index';
import Setting from 'containers/Setting/index';
import {isLoading, isAllowed} from 'store/selectors';
import {USER_DATA, MENU_SUBSCRIPTION, MENU_API_KEY, MENU_USAGE, MENU_SERVICE_USAGE, MENU_SERVICE, MENU_ACCESS_LOG} from "constants/ActionTypes";
import {getUser} from '../actions/Auth';

import Landing from './routes/Landing';
import Subscription from './routes/Subscription';
import APIKey from './routes/APIKey';
import Usage from './routes/Usage';
import ServiceUsage from './routes/ServiceUsage';
import APIMarket from './routes/APIMarket';
import Service from './routes/Service';
import Package from './routes/Package';
import AccessLog from './routes/AccessLog';

const {Content} = Layout;

class App extends React.Component {

  componentDidMount() {
    const {token, history, authUser, getUser} = this.props;
    if (token)
      axios.defaults.headers.common['Authorization'] = "Bearer " + token;
    else
      history.push('/');
    if (token && !authUser) getUser();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const {token, history} = this.props;
    if (!token) history.push('/');
  }

  render() {
    const {match, loading, isAllowed, authUser} = this.props;

    return (
      <div id={'components-layout-demo-custom-trigger'}>
        <Spin spinning={loading} size={'large'}>
          {authUser ?
          <Layout>
            <Sidebar/>
            <Layout className="site-layout" style={{marginLeft: 80, height: '100vh', overflow: 'auto'}}>
              <Navbar/>
              {authUser && authUser.change_email && <Alert message={
                <span><Typography.Text strong>Email Verification. </Typography.Text> Please accept our email verification to <Typography.Text strong>{authUser.change_email}</Typography.Text></span>
              } banner />}
              <Content style={{
                margin: '24px 16px',
                minHeight: 280,
              }}>

                <Switch>

                  <Redirect exact from={`${match.url}/`} to={`${match.url}/api-market`} />
                  
                  <Route path={`${match.url}/landing`} component={Landing}/>
                  <Route path={`${match.url}/api-market`} component={APIMarket}/>
                  {isAllowed(MENU_API_KEY) && <Route path={`${match.url}/api-key`} component={APIKey}/>}
                  {isAllowed(MENU_USAGE) && <Route path={`${match.url}/usage`} component={Usage}/>}

                  {isAllowed(MENU_SERVICE) && <Route path={`${match.url}/service`} component={Service}/>}
                  {isAllowed(MENU_SERVICE) && <Route path={`${match.url}/package`} component={Package}/>}
                  {isAllowed(MENU_SUBSCRIPTION) && <Route path={`${match.url}/subscription`} component={Subscription}/>}
                  {isAllowed(MENU_SERVICE_USAGE) && <Route path={`${match.url}/service-usage`} component={ServiceUsage}/>}
                  {isAllowed(MENU_ACCESS_LOG) && <Route path={`${match.url}/access-log`} component={AccessLog}/>}
                  {/*<ProtectedRoute allowed={isAllowed(MENU_API_KEY)}*/}
                  {/*                component={<Route path={`${match.url}/api-key`} component={APIKey}/>}/>*/}
                  {/*<ProtectedRoute allowed={isAllowed(MENU_SUBSCRIPTION)}*/}
                  {/*                component={<Route path={`${match.url}/subscription`} component={Subscription}/>}/>*/}
                  {/*Other menu route*/}

                  <Redirect to="/404" />

                </Switch>

                <Setting/>

              </Content>
            </Layout>
          </Layout> : <div style={{height: '100vh'}}/>}
        </Spin>
      </div>
    );
  }
}

const mapStateToProps = (store) => {
  const {auth: {token, authUser}} = store;
  return {
    loading: isLoading(store, USER_DATA),
    isAllowed: isAllowed(store),
    token, authUser
  }
};

export default withRouter(connect(mapStateToProps, {getUser})(App));

// const ProtectedRoute = ({allowed, component}) =>
//   allowed ? component : <Redirect to={'/403'}/>;