import React from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import {Row, Col, Card, Result, Button, Spin, Form, Input, Descriptions, Typography, Space} from 'antd';
import {LockOutlined} from '@ant-design/icons';

import {isLoading} from 'store/selectors';
import {GET_SINGLE_USER, VERIFY_PASSWORD} from "constants/ActionTypes";
import {verifyPassword} from 'actions/Verification';
import {getForgotUser} from 'actions/Auth';

class PasswordVerification extends React.Component {

  componentDidMount() {
    const query = new URLSearchParams(this.props.location.search);
    if (query.get('token')) this.props.getForgotUser(query.get('token'));
  }
  
  onFinish = ({password}) => {
    const query = new URLSearchParams(this.props.location.search);
    this.props.verifyPassword(query.get('token'), password)
  };

  render() {
    const {loading, user, password} = this.props;

    return (
      <Row type="flex" justify="center" align="middle"
           style={{minHeight: '100vh', backgroundImage: `url(/background.png)`}}>
        <Col>
          <Card style={{width: 400, minHeight: 360}}>
            <Spin spinning={loading.get}>
              {loading.get ? <div style={{minHeight: 300}}/> :
                <>
                  {user ?
                    <>
                      {!password ?
                        <Space direction={'vertical'} style={{width: '100%'}}>
                          <Row type="flex" justify="center">
                            <Col><img src={'/logo-mysights.png'} alt={'logo mysights'}
                                      style={{maxWidth: '150px', marginBottom: '10px'}}/></Col>
                          </Row>
                          <Descriptions column={1} bordered>
                            <Descriptions.Item
                              label={<Typography.Text strong>Fullname</Typography.Text>}>{user.fullname}</Descriptions.Item>
                            <Descriptions.Item
                              label={<Typography.Text strong>Username</Typography.Text>}>{user.username}</Descriptions.Item>
                            <Descriptions.Item
                              label={<Typography.Text strong>Email</Typography.Text>}>{user.email}</Descriptions.Item>
                          </Descriptions>
                          <Form
                            name="forgot"
                            className="forgot-form"
                            onFinish={this.onFinish}>
                            <Form.Item
                              name="password"
                              hasFeedback
                              rules={[
                                {required: true, message: 'Please input your Password!',},
                                {min: 8, message: 'Password must be at least 8 characters!'}
                              ]}
                            >
                              <Input.Password
                                prefix={<LockOutlined className="site-form-item-icon"/>}
                                type="password"
                                placeholder="New Password"
                              />
                            </Form.Item>
                            <Form.Item
                              name="confirmPassword"
                              dependencies={['password']}
                              hasFeedback
                              rules={[
                                {required: true, message: 'Please input your Password again!',},
                                ({getFieldValue}) => ({
                                  validator(rule, value) {
                                    if (!value || getFieldValue('password') === value) {
                                      return Promise.resolve();
                                    }
                                    return Promise.reject('The two passwords that you entered do not match!');
                                  },
                                }),
                              ]}
                            >
                              <Input.Password
                                prefix={<LockOutlined className="site-form-item-icon"/>}
                                type="password"
                                placeholder="Confirm Password"
                              />
                            </Form.Item>
                            <Form.Item>
                              <Button type="primary" htmlType="submit" className="forgot-form-button"
                                      loading={loading.submit}
                                      block>
                                Submit
                              </Button>
                            </Form.Item>
                          </Form>
                        </Space> :
                        <Result
                          status="success"
                          title="Success"
                          subTitle="Mail verification success!"
                          extra={[
                            <Link to={'/'}><Button type="primary" key="console">Go Home</Button></Link>
                          ]}
                        />
                      }
                    </> :
                    <Result
                      status="error"
                      title="Invalid Token"
                      subTitle="Password verification failed!"
                      extra={[
                        <Link to={'/'}><Button type="primary" key="console">Go Home</Button></Link>
                      ]}
                    />
                  }
                </>}
            </Spin>
          </Card>
        </Col>
      </Row>
    );
  }

}

const mapStateToProps = (store) => {
  const {user: {user}, verification: {status: {password}}} = store;
  return {
    user, password, loading: {
      get: isLoading(store, GET_SINGLE_USER),
      submit: isLoading(store, VERIFY_PASSWORD)
    }
  }
};

export default connect(mapStateToProps, {getForgotUser, verifyPassword})(PasswordVerification)