import React from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import moment from "moment";
import _ from "lodash";

am4core.useTheme(am4themes_animated);

class UsageChart extends React.Component {

  createDateArray = (start, end) => {
    var days = [];
    var day = start;

    while (day <= end) {
      days.push(day.toDate());
      day = day.clone().add(1, 'day');
    }

    return days;
  };
  
  componentDidMount() {
    this.renderChart()
  }
  
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (this.chart) this.chart.dispose();
    
    this.renderChart()
  }

  componentWillUnmount() {
    if (this.chart) this.chart.dispose();
  }
  
  renderChart() {
    const {identifier, data} = this.props;

    // Create chart instance
    var chart = am4core.create(identifier, am4charts.XYChart);

    // Add data
    chart.data = data;

    // Create axes
    var dateAxis = chart.xAxes.push(new am4charts.DateAxis());
    dateAxis.startLocation = 0.5;
    dateAxis.endLocation = 0.5;

    // Create value axis
    var valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.min = 0;
    // if (!_.isNull(limit)) valueAxis.max = limit;

    // Create series
    var series = chart.series.push(new am4charts.LineSeries());
    series.dataFields.valueY = "usages";
    series.dataFields.dateX = "date";
    series.strokeWidth = 3;
    series.tooltipText = "{valueY.value}";
    series.fillOpacity = 0.1;

    // Create a range to change stroke for values below 0
    // var range = valueAxis.createSeriesRange(series);
    // range.value = 0;
    // range.endValue = -1000;
    // range.contents.stroke = chart.colors.getIndex(4);
    // range.contents.fill = range.contents.stroke;
    // range.contents.strokeOpacity = 0.7;
    // range.contents.fillOpacity = 0.1;

    // Add cursor
    chart.cursor = new am4charts.XYCursor();
    chart.cursor.xAxis = dateAxis;
    chart.scrollbarX = new am4core.Scrollbar();

    series.tooltip.getFillFromObject = false;
    series.tooltip.adapter.add("x", (x, target) => {
      if (series.tooltip.tooltipDataItem.valueY < 0) {
        series.tooltip.background.fill = chart.colors.getIndex(4);
      } else {
        series.tooltip.background.fill = chart.colors.getIndex(0);
      }
      return x;
    });
    
    // if (!_.isNull(limit)) {
    //   // Add a guide
    //   let guide = valueAxis.axisRanges.create();
    //   guide.value = limit;
    //   guide.grid.stroke = am4core.color("#396478");
    //   guide.grid.strokeWidth = 1;
    //   guide.grid.strokeOpacity = 1;
    //   guide.grid.strokeDasharray = "3,3";
    //   guide.label.inside = true;
    //   guide.label.text = "Limit";
    //   guide.label.fill = guide.grid.stroke;
    //   guide.label.verticalCenter = "top";
    // }

    this.chart = chart;
  }

  render() {
    const {identifier} = this.props;

    return <div id={identifier} style={{width: "100%", height: "300px"}}/>
  }

}

export default UsageChart;