import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';
import moment from 'moment';

import {isLoading} from 'store/selectors';
import {
  GET_ALL_SERVICE,
  GET_SUBSCRIPTION,
} from "constants/ActionTypes";
import {getAllService} from 'actions/Service';
import {getSubscriptionByService, addSubscription, editSubscription} from 'actions/Subscription';
import {getPackageByService} from 'actions/Package';
import {getAllUser} from 'actions/User';
import {onResetStatus, onResetDataState} from 'actions/Common';
import PackageTag from "components/Tags/Package";
import SubscriptionStatusTag from "components/Tags/SubscriptionStatus";

import {Icon} from '@ant-design/compatible';
import {FundProjectionScreenOutlined} from '@ant-design/icons';
import {
  Card,
  Menu,
  Layout,
  Empty,
  Spin,
  Table,
  Space,
  Typography,
  Tooltip,
  Button
} from 'antd';

class Subscription extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      selectedService: null
    }
  }

  componentDidMount() {
    this.props.getAllService()
  }

  componentWillUnmount() {
    this.props.onResetDataState()
  }

  onShowUsage = (id) => {
    const {services, history, match} = this.props;
    const serviceList = _(services).map('services').flatten().value();
    const {code} = _.find(serviceList, {id: this.state.selectedService});
    history.push(`${match.url}/${code}/${id}`)
  };
  
  // Menu

  onSelectMenu = ({key}) => {
    // eslint-disable-next-line
    const [item, submenu, id] = _.split(key, '-');
    this.props.getSubscriptionByService(id);
    this.setState({selectedService: parseInt(id)});
  };

  render() {

    const {services, loading, subscriptions} = this.props;

    const column = [
      {
        title: 'Account',
        dataIndex: 'account',
        key: 'account',
        filters: _(subscriptions).map(({account}) => {
          return {text: account.fullname, value: account.fullname}
        }).uniqBy('value').value(),
        onFilter: (value, {account}) => account.fullname.includes(value),
        render: ({fullname, email}) => (<div>
          <Typography.Text strong>{fullname}</Typography.Text><br/>
          <Typography.Text type={'secondary'}>{email}</Typography.Text>
        </div>)
      },
      {
        title: 'Current Subscription',
        children: [
          {
            title: 'Subs ID',
            dataIndex: 'code',
            key: 'code',
            render: (code, {status}) => (status === 'active' && code)
          },
          {
            title: 'Package',
            dataIndex: 'package',
            key: 'package',
            render: (_package, {status}) => (status === 'active' && _package && <PackageTag name={_package.name}/>)
          },
          {
            title: 'Start Date',
            dataIndex: 'start_date',
            key: 'start_date',
            render: (date, {status}) => status === 'active' && date ? moment(date).format('ll') : null
          },
          {
            title: 'End Date',
            dataIndex: 'end_date',
            key: 'end_date',
            render: (date, {status}) => status === 'active' && date ? moment(date).format('ll') : null
          },
        ]
      },
      {
        title: 'Action',
        key: 'action',
        render: (text, record) => (
          <Space>
            <Tooltip title={'Usage'}><Button type="link" icon={<FundProjectionScreenOutlined/>}
                                             onClick={() => this.onShowUsage(record.account.id)}/></Tooltip>
          </Space>
        ),
      },
    ];

    return (
      <Space direction={'vertical'} style={{width: '100%'}}>
        <Typography.Title level={4}>Service</Typography.Title>
        <Card bordered={false}>
          <Spin spinning={loading.service}>
            <Layout style={{minHeight: 600, border: '1px solid #f0f0f0'}}>
              <Layout.Sider className="site-layout-background" width={200}>

                {services.length ?
                  <Menu
                    mode="inline"
                    defaultOpenKeys={['submenu-0']}
                    style={{height: '100%'}}
                    onSelect={this.onSelectMenu}>
                    {_.map(services, ({id, name, icon, services}, index) => {
                      return <Menu.SubMenu key={'submenu-' + index} icon={<Icon type={icon}/>} title={name}>
                        {_.map(services, ({id, name, icon}, i) =>
                          <Menu.Item key={'item-' + index + '-' + id} icon={<Icon type={icon}/>}>{name}</Menu.Item>)}
                      </Menu.SubMenu>
                    })}
                  </Menu>
                  : <Empty/>}

              </Layout.Sider>
              <Layout.Content style={{padding: '24px'}}>
                <Spin spinning={loading.subs}>

                  {subscriptions.length ?
                    <Table columns={column}
                           dataSource={_(subscriptions).sortBy('status').uniqBy('account.id').value()}
                           rowKey={'id'}
                           style={{width: '100%'}}/>
                    : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE}/>}

                </Spin>
              </Layout.Content>
            </Layout>
          </Spin>
        </Card>
      </Space>
    );
  }

}

const mapStateToProps = (store) => {
  const {service: {services}, subscription: {subscriptions, status: {created, edited}}} = store;
  return {
    loading: {
      service: isLoading(store, GET_ALL_SERVICE),
      subs: isLoading(store, GET_SUBSCRIPTION),
    },
    services, subscriptions, created, edited
  }
};

export default connect(mapStateToProps, {
  getAllService,
  getSubscriptionByService,
  addSubscription,
  editSubscription,
  getPackageByService,
  getAllUser,
  onResetStatus,
  onResetDataState
})(Subscription);