import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';
import moment from 'moment';

import {isLoading} from 'store/selectors';
import {GET_SUBSCRIPTION_TIMELINE} from "constants/ActionTypes";
import {getSubscriptionTimeline} from 'actions/Subscription';

import PackageCard from "components/PackageCard";

import {
  TagsOutlined,
  UpCircleOutlined,
  CloseCircleOutlined
} from '@ant-design/icons';
import {
  Spin,
  Space,
  Typography,
  Row, Col,
  Timeline,
  Card,
  Tag,
  List
} from 'antd';

class SubscriptionAddOn extends React.Component {

  componentDidMount() {
    this.props.getSubscriptionTimeline(this.props.subs)
  }

  render() {

    const {loading, timeline, subscription} = this.props;

    const {start_date, end_date, package: _package} = subscription;

    return (
      <Spin spinning={loading}>
        <Timeline style={{marginTop: 8}}>

          <Timeline.Item dot={<UpCircleOutlined style={{fontSize: '16px'}}/>} color={'green'}>
            <Space direction={'vertical'} style={{width: '100%'}}>
              <Space>
                <Typography.Text>Subscription start ({moment(start_date).format('ll')})</Typography.Text>
                {moment().isSame(moment(start_date), 'day') && <Tag color="blue">NOW</Tag>}
              </Space>
              <Row gutter={[16]}>
                <Col flex="400px">
                  <Card className={'service-box'}>
                    <PackageCard package={_package}/>
                  </Card>
                </Col>
                <Col flex="auto">
                  <List
                    itemLayout="horizontal"
                    dataSource={[]}
                    header={<div><TagsOutlined/> Add-ons</div>}
                    renderItem={({limit: {code, reset}, amount, is_unlimited}) => (
                      <List.Item>
                        <List.Item.Meta
                          title={<Typography.Text strong>{_.capitalize(code)}</Typography.Text>}
                          description={reset}
                        />
                        <div>{(is_unlimited) ?
                          <Typography.Text strong type={'success'}>Unlimited</Typography.Text> : amount}</div>
                      </List.Item>
                    )}
                  />
                </Col>
              </Row>
            </Space>
          </Timeline.Item>

          {_.map(timeline, ({start_date, end_date, package: _package, addOns}) =>
            <Timeline.Item>
              <Space direction={'vertical'} style={{width: '100%'}}>
                <Space>
                  <Typography.Text>
                    {moment(start_date).isSame(moment(end_date), 'day') ?
                      moment(start_date).format('ll') :
                      moment(start_date).format('ll') + ' - ' + moment(end_date).format('ll')}
                  </Typography.Text>
                  {moment().isBetween(moment(start_date), moment(end_date), 'day', '[]') &&
                  <Tag color="blue">NOW</Tag>}
                </Space>
                <Row gutter={[16]}>
                  <Col flex="400px">
                    <Card className={'service-box'}>
                      <PackageCard package={_package}/>
                    </Card>
                  </Col>
                  <Col flex="auto">
                    <List
                      itemLayout="horizontal"
                      dataSource={addOns}
                      header={<div><TagsOutlined/> Add-ons</div>}
                      renderItem={({limit: {code, reset}, amount, is_unlimited}) => (
                        <List.Item>
                          <List.Item.Meta
                            title={<Typography.Text strong>{_.capitalize(code)}</Typography.Text>}
                            description={reset}
                          />
                          <div>{(is_unlimited) ? <Typography.Text strong type={'success'}>Unlimited</Typography.Text> : '+' + amount}</div>
                        </List.Item>
                      )}
                    />
                  </Col>
                </Row>
              </Space>
            </Timeline.Item>)}

          <Timeline.Item dot={<CloseCircleOutlined style={{fontSize: '16px'}}/>} color={'grey'}>
            <Space>
              <Typography.Text>Subscription end ({moment(end_date).format('ll')})</Typography.Text>
              {moment().isSame(moment(end_date), 'day') && <Tag color="blue">NOW</Tag>}
            </Space>
          </Timeline.Item>

        </Timeline>
      </Spin>
    )

  }

}

const mapStateToProps = (store) => {
  const {subscription: {subscription, timeline}} = store;
  return {
    loading: isLoading(store, GET_SUBSCRIPTION_TIMELINE),
    subscription, timeline
  }
};

export default connect(mapStateToProps, {
  getSubscriptionTimeline
})(SubscriptionAddOn);