import React from 'react';
import {connect} from 'react-redux';

import {isLoading} from 'store/selectors';
import {GET_API_KEY, ADD_API_KEY} from "constants/ActionTypes";

import {getAPIKey, addAPIKey} from 'actions/APIKey';
import {onResetStatus, onResetDataState} from 'actions/Common';

import {CopyOutlined, PlusOutlined} from '@ant-design/icons';
import {
  Card,
  Spin,
  Table,
  Button,
  Space,
  Tooltip,
  Typography
} from 'antd';

class APIKey extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      copied: null,
      column: [
        {
          title: 'API Key',
          dataIndex: 'api_key',
          key: 'api_key',
          render: (key) => (
            <Space>
              {key}
              <Tooltip title={this.state.copied === key ? 'Copied' : 'Copy'}>
                <Button type="link" icon={<CopyOutlined/>} onClick={() => this.onCopy(key)}/>
              </Tooltip>
            </Space>
          )
        }
      ]
    }
  }

  componentDidMount() {
    this.props.getAPIKey()
  }
  
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.created && this.props.created) {
      this.props.getAPIKey();
      this.props.onResetStatus();
    }
  }
  
  componentWillUnmount() {this.props.onResetDataState()}

  onAdd = () => this.props.addAPIKey();

  onCopy = (key) => {
    var dummy = document.createElement("textarea");
    document.body.appendChild(dummy);
    dummy.value = key;
    dummy.select();
    document.execCommand("copy");
    document.body.removeChild(dummy);
    this.setState({copied: key});
  };
  

  render() {
    const {loading, keys} = this.props;
    const {column} = this.state;

    return (
      <Space direction={'vertical'} style={{width: '100%'}}>
        <Typography.Title level={4}>API Key</Typography.Title>
        <Card bordered={false} style={{minHeight: 600}}>
          <Spin spinning={loading.get}>
            <Space direction={'vertical'} style={{width: '100%'}}>
              <Button type="primary"
                      style={{float: 'right'}}
                      icon={<PlusOutlined/>}
                      loading={loading.add}
                      onClick={this.onAdd}>
                Add
              </Button>
              <Table columns={column}
                     dataSource={keys}
                     rowKey={'id'}
                     style={{width: '100%'}}/>
            </Space>
          </Spin>
        </Card>
      </Space>
    );
  }

}

const mapStateToProps = (store) => {
  const {apiKey: {keys, status: {created}}} = store;
  return {
    loading: {
      get: isLoading(store, GET_API_KEY),
      add: isLoading(store, ADD_API_KEY),
    },
    keys, created
  }
};

export default connect(mapStateToProps, {
  getAPIKey, addAPIKey, onResetStatus, onResetDataState
})(APIKey);