import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';

import {isLoading} from 'store/selectors';
import {getMarketService} from 'actions/Service';
import {onResetDataState} from 'actions/Common';
import {
  GET_ALL_SERVICE
} from "constants/ActionTypes";

import {Icon} from '@ant-design/compatible';
import {SearchOutlined} from '@ant-design/icons';
import {
  Space,
  Typography,
  Form,
  Input,
  Row, Col,
  Spin,
  Empty,
  Divider,
  Card,
  Avatar,
  Tag
} from 'antd';

class Market extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      search: null,
      group: null,
    }
  }

  componentDidMount() {
    this.props.getMarketService();
  }

  componentWillUnmount() {
    this.props.onResetDataState()
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!_.isEqual(prevState, this.state)) {
      const {search, group} = this.state; var condition = {};
      if (search) condition = {...condition, search};
      if (group) condition = {...condition, group: group.id};
      this.props.getMarketService(condition);
    }
  }

  onSearch = ({search}) => {
    if (search) this.setState({search});
  };

  onFilter = ({id, name}) => {
    this.setState({group: {id, name}});
  };

  onClearSearch = (kind) => {
    this.setState({[kind]: null});
  };

  render() {
    const {services, loading, history, match} = this.props;
    const {search, group} = this.state;

    const showedService = _.filter(services, ({services}) => services.length);

    return(
      <Space direction={'vertical'} style={{width: '100%'}}>
        <Row type="flex" justify="center" align="middle" style={{minHeight: 250, backgroundImage: `url(/background.png)`, backgroundSize: 'cover'}}>
          <Col>
            <Typography.Title level={3} style={{marginBottom: 0}}>Easily find services that suit your business</Typography.Title>
            <Typography.Text strong>supported by <img className={'logo'} src={process.env.PUBLIC_URL + '/logo-mysights.png'} alt={'icon'} style={{height: 24, margin: '10px 0'}}/></Typography.Text>
            <Form
              name="login"
              className="login-form"
              onFinish={this.onSearch}>
              <Form.Item name="search">
                <Input size="large" placeholder="Search" prefix={<SearchOutlined />} />
              </Form.Item>
            </Form>
          </Col>
        </Row>
        <Spin spinning={loading}>
          <div style={{minHeight: 250, margin: 16}}>
            <Row gutter={[16]}>
              <Col span={4}>
                <Typography.Title level={5}>Service Group</Typography.Title>
                <Space size={'small'} direction="vertical">
                  {_.map(services, ({id, name, services}) => (
                    <Typography.Link onClick={() => this.onFilter({id, name})}>{name} {services.length ? <>({services.length})</> : null}</Typography.Link>
                  ))}
                </Space>
              </Col>
              <Col span={20}>
                <Space style={{marginBottom: 16}}>
                  {search && <Tag closable color="blue" onClose={() => this.onClearSearch('search')}>
                    <Typography.Text strong>Search </Typography.Text>: {search}
                  </Tag>}
                  {group && <Tag closable color="blue" onClose={() => this.onClearSearch('group')}>
                    <Typography.Text strong>Group </Typography.Text>: {group.name}
                  </Tag>}
                </Space>
                {showedService.length ?
                  <Space direction="vertical" size={'large'} style={{width: '100%'}}>
                    {_.map(showedService, ({name: group_name, services}) => (
                      <div>
                        <Typography.Title level={5} style={{marginBottom: 0}}>{group_name}</Typography.Title>
                        <Divider style={{margin: '12px 0'}}/>
                        <Row gutter={[16]}>
                          {_.map(services, ({code, icon, name, description}) => (
                            <Col {...{xxl: 6, xl: 8, lg: 12, sm: 24}}>
                              <Typography.Link onClick={() => history.push(`${match.url}/${code}`)}>
                                <Card className={'service-box'}>
                                  <Card.Meta
                                    avatar={<Avatar style={{color: '#f56a00', backgroundColor: '#fde3cf'}}
                                                    icon={<Icon type={icon}/>}/>}
                                    title={name}
                                    description={group_name}
                                    style={{marginBottom: 16}}
                                  />
                                  <Typography.Paragraph ellipsis={{rows: 2}} style={{margin: 0}}>{description}</Typography.Paragraph>
                                </Card>
                              </Typography.Link>
                            </Col>
                          ))}
                        </Row>
                      </div>
                    ))}
                  </Space>
                  : <Empty/>
                }

              </Col>
            </Row>
          </div>
        </Spin>
      </Space>
    );
  }

}

const mapStateToProps = (store) => {
  const {service: {services}} = store;
  return {
    loading: isLoading(store, GET_ALL_SERVICE),
    services
  }
};

export default connect(mapStateToProps, {getMarketService, onResetDataState})(Market);