import {VERIFY_EMAIL, VERIFY_PASSWORD, RESET_STATUS} from "../constants/ActionTypes";

const INIT_STATE = {
  status: {
    email: null,
    password: null
  }
};

const Auth = (state = INIT_STATE, action) => {
  switch (action.type) {
    case VERIFY_EMAIL:
      return {...state, status: {...state.status, email: action.success}};
    case VERIFY_PASSWORD:
      return {...state, status: {...state.status, password: action.success}};
    case RESET_STATUS:
      return {...state, status: INIT_STATE.status};
    default:
      return state;
  }
};

export default Auth;