import React from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import { Row, Col, Card, Result, Button, Spin } from 'antd';

import {isLoading} from 'store/selectors';
import {VERIFY_EMAIL} from "constants/ActionTypes";
import {verifyEmail} from 'actions/Verification';

class EmailVerification extends React.Component {

  componentDidMount() {
    const query = new URLSearchParams(this.props.location.search);
    if (query.get('token')) this.props.verifyEmail(query.get('token'));
  }

  render() {
    const {loading, email} = this.props;

    return (
      <Row type="flex" justify="center" align="middle" style={{minHeight: '100vh', backgroundImage: `url(/background.png)`}}>
        <Col>
          <Card style={{width: 400, minHeight: 360}}>
            <Spin spinning={loading}>
              {loading ? <div style={{minHeight: 300}}/> :
                <>
                  {email ?
                    <Result
                      status="success"
                      title="Success"
                      subTitle="Mail verification success!"
                      extra={[
                        <Link to={'/'}><Button type="primary" key="console">Go Home</Button></Link>
                      ]}
                    /> :
                    <Result
                      status="error"
                      title="Invalid Token"
                      subTitle="Mail verification failed!"
                      extra={[
                        <Link to={'/'}><Button type="primary" key="console">Go Home</Button></Link>
                      ]}
                    />
                  }
                </>}
            </Spin>
          </Card>
        </Col>
      </Row>
    );
  }

}

const mapStateToProps = (store) => {
  const {verification: {status: {email}}} = store;
  return {email, loading: isLoading(store, VERIFY_EMAIL)}
};

export default connect(mapStateToProps, {verifyEmail})(EmailVerification)