import React from 'react';

import DocsHeader from "../DocsHeader";

import {Table, Typography, Space, Tooltip, Row, Col, Anchor} from 'antd';
import { magenta } from '@ant-design/colors';

import { PrismLight as SyntaxHighlighter } from 'react-syntax-highlighter';
import json from 'react-syntax-highlighter/dist/esm/languages/prism/json';
import material from 'react-syntax-highlighter/dist/esm/styles/prism/material-light';

SyntaxHighlighter.registerLanguage('json', json);

const Docs = () => {

  const GATEWAY_URL = 'https://api.mysights.id/api';

  const column = [
    {
      title: 'Property',
      dataIndex: 'property',
      key: 'propery',
      render: (text, {required}) =>
        <Typography.Text strong>{text} {required &&
        <Tooltip title={'required'}><Typography.Text type={'danger'}>*</Typography.Text></Tooltip>}</Typography.Text>
    },
    {
      title: 'Description',
      dataIndex: 'description',
      key: 'description'
    },
    {
      title: 'Type',
      dataIndex: 'type',
      key: 'type',
      render: (text) => <Typography.Text style={{color: magenta[6]}}>{text}</Typography.Text>
    },
    {
      title: 'Default',
      dataIndex: 'default',
      key: 'default',
      render: (text) => <>{text ? text : '-'}</>
    },
  ];

  return (
    <Row wrap={false} gutter={[16]}>
      <Col flex="auto">
        <Space direction={'vertical'} style={{width: '100%'}}>
          <DocsHeader level={3} text={'API Request'} link={'#request'}/>

          <DocsHeader level={4} text={'URL'} link={'#request-url'}/>
          <Typography.Text code
                           copyable={{text: `${GATEWAY_URL}/soap/route/route-optimization?[params]`}}><Typography.Text
            strong
            type="warning">POST</Typography.Text> {GATEWAY_URL}/soap/route/route-optimization?[params]</Typography.Text>

          <DocsHeader level={4} text={'Params'} link={'#request-params'}/>
          <Table bordered pagination={false} columns={column} dataSource={[
            {
              property: 'api_key',
              description: 'Your api key provided by mysights portal',
              type: 'string',
              required: true
            },
            {
              property: 'routing_type',
              description: <ul>
                <li><Typography.Text code>point</Typography.Text> : route use exact latlong to determine pool-to-point
                  distance
                </li>
                <li><Typography.Text code>realtime</Typography.Text> : route use realtime actual pool-to-point distance
                  using distance-matrix service
                </li>
                <li><Typography.Text code>historical</Typography.Text> : route use pre-registered pool-to-point distance
                  using distance-matrix service, unregistered point will be rejected
                </li>
                <li><Typography.Text code>historicalplus</Typography.Text> : route use pre-registered pool-to-point distance
                  using distance-matrix service, unregistered point will be accepted (+additional charge)
                </li>
              </ul>,
              type: <>
                <Typography.Text code>point</Typography.Text> | <Typography.Text
                code>realtime</Typography.Text> | <Typography.Text code>historical</Typography.Text> | <Typography.Text
                code>historicalplus</Typography.Text>
              </>,
              default: <Typography.Text code>point</Typography.Text>
            },
            {
              property: 'multiple_days',
              description: 'Routing with multiple days timeframe.',
              type: 'boolean',
              default: <Typography.Text code>false</Typography.Text>
            }
          ]}/>

          <DocsHeader level={4} text={'Body'} link={'#request-body'}/>
          <Table bordered pagination={false} columns={column} dataSource={[
            {
              property: 'deliveries',
              description: 'Specification of point and goods to be delivered',
              type: <><Typography.Link href={'#request-body-delivery'}>Delivery</Typography.Link>[]</>,
              required: true
            },
            {
              property: 'vehicles',
              description: 'Specification of vehicle used to deliver good',
              type: <><Typography.Link href={'#request-body-vehicle'}>Vehicle</Typography.Link>[]</>,
              required: true
            },
            {
              property: 'depot',
              description: 'Specification of depot/pool for delivery central',
              type: <><Typography.Link href={'#request-body-depot'}>Depot</Typography.Link></>,
              required: true
            }
          ]}/>

          <DocsHeader level={5} text={'Delivery'} link={'#request-body-delivery'}/>
          <Typography.Text>One of the <Typography.Text code>deliveries</Typography.Text> prop for describing the
            customer point.</Typography.Text>
          <Table bordered pagination={false} columns={column} dataSource={[
            {
              property: 'customer_id',
              description: 'Key of customer point / customer name',
              type: 'string',
              required: true
            },
            {
              property: 'delivery_id',
              description: 'Unique key of delivery',
              type: 'string',
              required: true
            },
            {
              property: 'delivery_date',
              description: 'Date of delivery',
              type: 'string',
              required: true
            },
            {
              property: 'weight_total',
              description: 'Total weight of goods to be delivered, unit must be the same as the unit weight used on the vehicle',
              type: 'float',
              required: true
            },
            {
              property: 'volume_total',
              description: 'Total volume of goods to be delivered, unit must be the same as the unit volume used on the vehicle',
              type: 'float',
              required: true
            },
            {
              property: 'latitude',
              description: 'Geographic coordinate that specifies the north–south position of customer point',
              type: 'float',
              required: true
            },
            {
              property: 'longitude',
              description: 'Geographic coordinate that specifies the east-west position of customer point',
              type: 'float',
              required: true
            },
            {
              property: 'open',
              description: 'Time window / time visit for the customer point open time (e.g. 08:00)',
              type: 'string',
              required: true
            },
            {
              property: 'close',
              description: 'Time window / time visit for the customer point close time (e.g. 17:00)',
              type: 'string',
              required: true
            },
            {
              property: 'vehicle_type',
              description: 'Restrictions on the types of vehicles allowed to access the delivery point',
              type: 'string[]',
              required: true
            }
          ]}/>

          <DocsHeader level={5} text={'Vehicle'} link={'#request-body-vehicle'}/>
          <Typography.Text>One of the <Typography.Text code>vehicles</Typography.Text> prop for describing the vehicle
            used.</Typography.Text>
          <Table bordered pagination={false} columns={column} dataSource={[
            {
              property: 'id',
              description: 'Unique key of vehicle / license plate',
              type: 'string',
              required: true
            },
            {
              property: 'type',
              description: 'Vehicle type (e.g. truck, box)',
              type: 'string',
              required: true
            },
            {
              property: 'supir',
              description: 'Driver name',
              type: 'string',
              required: true
            },
            {
              property: 'kenek',
              description: 'Co-driver name',
              type: 'float',
              required: true
            },
            {
              property: 'volume',
              description: 'Vehicle volume (delivery volume constraint)',
              type: 'float',
              required: true
            },
            {
              property: 'weight',
              description: 'Vehicle weight (delivery weight constraint)',
              type: 'float',
              required: true
            }
          ]}/>

          <DocsHeader level={5} text={'Depot'} link={'#request-body-depot'}/>
          <Typography.Text><Typography.Text code>depot</Typography.Text> prop for describing the
            depot.</Typography.Text>
          <Table bordered pagination={false} columns={column} dataSource={[
            {
              property: 'depot_id',
              description: 'Unique key of depot/pool',
              type: 'string',
              required: true
            },
            {
              property: 'latitude',
              description: 'Geographic coordinate that specifies the north–south position of depot/pool',
              type: 'float',
              required: true
            },
            {
              property: 'longitude',
              description: 'Geographic coordinate that specifies the east-west position of depot/pool',
              type: 'float',
              required: true
            },
            {
              property: 'open',
              description: 'Time window / time visit for the depot open time (e.g. 08:00)',
              type: 'string',
              required: true
            },
            {
              property: 'close',
              description: 'Time window / time visit for the depot close time (e.g. 17:00)',
              type: 'string',
              required: true
            }
          ]}/>

          <DocsHeader level={5} text={'Request Example'} link={'#request-body-example'}/>
          <SyntaxHighlighter language="json" style={material}>
            {JSON.stringify(require('./request.json'), null, "\t")}
          </SyntaxHighlighter>

          <DocsHeader level={3} text={'API Response'} link={'#response'}/>

          <DocsHeader level={4} text={'Body'} link={'#response-body'}/>
          <Table bordered pagination={false} columns={column} dataSource={[
            {
              property: 'status_code',
              description: 'Response status code',
              type: 'number',
            },
            {
              property: 'message',
              description: 'Response message',
              type: 'string',
            },
            {
              property: 'data',
              description: 'Response data result',
              type: <><Typography.Link href={'#response-body-data'}>Data</Typography.Link></>,
            }
          ]}/>

          <DocsHeader level={5} text={'Response Data'} link={'#response-body-data'}/>
          <Table bordered pagination={false} columns={column} dataSource={[
            {
              property: 'depot_id',
              description: 'Unique key of depot/pool result',
              type: 'string',
            },
            {
              property: 'detail',
              description: 'Route result of related depot',
              type: <><Typography.Link href={'#response-body-route'}>Route</Typography.Link>[]</>,
            }
          ]}/>

          <DocsHeader level={5} text={'Route'} link={'#response-body-route'}/>
          <Typography.Text>One of the<Typography.Text code>route</Typography.Text> prop for describing the
            route optimization result.</Typography.Text>
          <Table bordered pagination={false} columns={column} dataSource={[
            {
              property: 'SJ_id',
              description: 'Unique key of route (generated)',
              type: 'string',
            },
            {
              property: 'date',
              description: 'Date of route result',
              type: 'string',
            },
            {
              property: 'vehicle_id',
              description: 'Unique key of selected vehicle / license plate',
              type: 'string',
            },
            {
              property: 'distance',
              description: 'Total distance of one route (meters)',
              type: 'number',
            },
            {
              property: 'duration',
              description: 'Duration estimation needed of one route (minutes)',
              type: 'number',
            },
            {
              property: 'weight',
              description: 'Total load weight in one vehicle',
              type: 'float',
            },
            {
              property: 'trip',
              description: 'Detail of trip / point visited',
              type: <><Typography.Link href={'#response-body-trip'}>Trip</Typography.Link>[]</>,
            }
          ]}/>

          <DocsHeader level={5} text={'Trip'} link={'#response-body-trip'}/>
          <Typography.Text>One of the<Typography.Text code>trip</Typography.Text> prop for describing the
            trip / point visited.</Typography.Text>
          <Table bordered pagination={false} columns={column} dataSource={[
            {
              property: 'delivery_id',
              description: 'Unique key of delivery',
              type: 'string',
            },
            {
              property: 'customer_id',
              description: 'Key of customer point / customer name',
              type: 'string',
            },
            {
              property: 'sequence',
              description: 'Order of visit points',
              type: 'number',
            }
          ]}/>

          <DocsHeader level={5} text={'Response Example'} link={'#response-body-example'}/>
          <SyntaxHighlighter language="json" style={material}>
            {JSON.stringify(require('./response.json'), null, "\t")}
          </SyntaxHighlighter>

        </Space>
      </Col>
      <Col flex="none">
        <Anchor targetOffset={200}>
          <Anchor.Link href="#request" title="Request">
            <Anchor.Link href="#request-url" title="URL"/>
            <Anchor.Link href="#request-params" title="Params"/>
            <Anchor.Link href="#request-body" title="Body">
              <Anchor.Link href="#request-body-delivery" title="Delivery"/>
              <Anchor.Link href="#request-body-vehicle" title="Vehicle"/>
              <Anchor.Link href="#request-body-depot" title="Depot"/>
            </Anchor.Link>
            <Anchor.Link href="#request-body-example" title="Request Example"/>
          </Anchor.Link>
          <Anchor.Link href="#request" title="Response">
            <Anchor.Link href="#response-body" title="Body">
              <Anchor.Link href="#response-body-data" title="Data"/>
              <Anchor.Link href="#response-body-route" title="Route"/>
              <Anchor.Link href="#response-body-trip" title="Trip"/>
            </Anchor.Link>
            <Anchor.Link href="#response-body-example" title="Response Example"/>
          </Anchor.Link>
        </Anchor>
      </Col>
    </Row>
  );
};

export default Docs