import {ADD_API_KEY, GET_API_KEY, RESET_STATUS, RESET_DATA_STATE} from "../constants/ActionTypes";

const INIT_STATE = {
  keys: [],
  status: {
    created: null,
  }
};

const APIKey = (state = INIT_STATE, action) => {
  switch (action.type) {
    case GET_API_KEY:
      return {...state, keys: action.payload};
    case ADD_API_KEY:
      return {...state, status: {...state.status, created: true}};
    case RESET_STATUS:
      return {...state, status: INIT_STATE.status};
    case RESET_DATA_STATE:
      return INIT_STATE;
    default:
      return state;
  }
};

export default APIKey;