import React from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import {Form, Input, Button, Typography, Card, Row, Col} from 'antd';
import {UserOutlined, InboxOutlined} from '@ant-design/icons';

import {isLoading} from 'store/selectors';
import {userForgot, onResetStatus} from 'actions/Auth';
import {FORGOT} from "constants/ActionTypes";

class Forgot extends React.Component {

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.success && this.props.success)
      this.props.onResetStatus();
  }

  onFinish = (values) => this.props.userForgot(values);

  render() {
    const {loading} = this.props;

    return (
      <Row type="flex" justify="center" align="middle"
           style={{minHeight: '100vh', backgroundImage: `url(/background.png)`}}>
        <Col>
          <Card style={{width: 400}}>
            <Row type="flex" justify="center">
              <Col><img src={'/logo-mysights.png'} alt={'logo mysights'}
                        style={{maxWidth: '150px', marginBottom: '10px'}}/></Col>
            </Row>
            <Form
              name="forgot"
              className="forgot-form"
              onFinish={this.onFinish}
              style={{marginTop: 48}}
            >

              <Form.Item
                name="username"
                rules={[
                  {required: true, message: 'Please input your Username!',},
                ]}
              >
                <Input prefix={<UserOutlined className="site-form-item-icon"/>} placeholder="Username"/>
              </Form.Item>

              <Form.Item
                name="email"
                rules={[
                  {type: 'email', message: 'The input is not valid E-mail!',},
                  {required: true, message: 'Please input your Email!',},
                ]}
              >
                <Input prefix={<InboxOutlined className="site-form-item-icon"/>} placeholder="Email"/>
              </Form.Item>

              <Form.Item>
                <Link to={'/login'} style={{float: 'right'}}>
                  <Typography.Link>Login</Typography.Link>
                </Link>
              </Form.Item>

              <Form.Item>
                <Button type="primary" htmlType="submit" className="forgot-form-button" loading={!!loading} block>
                  Submit
                </Button>
              </Form.Item>

            </Form>
          </Card>
        </Col>
      </Row>
    )
  };

}

const mapStateToProps = (store) => {
  const {auth: {status: {forgot}}} = store;
  return {loading: isLoading(store, FORGOT), success: !!forgot}
};

export default connect(mapStateToProps, {userForgot, onResetStatus})(Forgot)