import {GET_USER, GET_SINGLE_USER, EDIT_USER, RESET_DATA_STATE, RESET_STATUS} from "../constants/ActionTypes";

const INIT_STATE = {
  user: null,
  users: [],
  status: {
    edited: null
  }
};

const Subscription = (state = INIT_STATE, action) => {
  switch (action.type) {
    case GET_USER:
      return {...state, users: action.payload};
    case GET_SINGLE_USER:
      return {...state, user: action.payload};
    case EDIT_USER:
      return {...state, status: {...state.status, edited: true}};
    case RESET_STATUS:
      return {...state, status: INIT_STATE.status};
    case RESET_DATA_STATE:
      return INIT_STATE;
    default:
      return state;
  }
};

export default Subscription;