import React from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import {Form, Input, Button, Typography} from 'antd';
import {UserOutlined, LockOutlined} from '@ant-design/icons';

import {isLoading} from '../store/selectors';
import {userSignIn, onResetStatus} from '../actions/Auth';
import {LOG_IN} from "../constants/ActionTypes";

class Login extends React.Component {

  onFinish = (values) => this.props.userSignIn(values);

  render() {
    const {loading} = this.props;

    return (
      <Form
        name="login"
        className="login-form"
        onFinish={this.onFinish}
      >

        <Form.Item
          name="username"
          rules={[
            {
              required: true,
              message: 'Please input your Username!',
            },
          ]}
        >
          <Input prefix={<UserOutlined className="site-form-item-icon"/>} placeholder="Username"/>
        </Form.Item>

        <Form.Item
          name="password"
          rules={[
            {
              required: true,
              message: 'Please input your Password!',
            },
          ]}
        >
          <Input
            prefix={<LockOutlined className="site-form-item-icon"/>}
            type="password"
            placeholder="Password"
          />
        </Form.Item>

        <Form.Item>
          <Link to={'/forgot'} style={{float: 'right'}}>
            <Typography.Link>Forgot password</Typography.Link>
          </Link>
        </Form.Item>
        
        <Form.Item>
          <Button type="primary" htmlType="submit" className="login-form-button" loading={!!loading} block>
            Log in
          </Button>
        </Form.Item>

      </Form>
    )
  };

}

const mapStateToProps = (store) => {
  const {auth: {status: {loggedIn}}} = store;
  return {loading: isLoading(store, LOG_IN), success: !!loggedIn}
};

export default connect(mapStateToProps, {userSignIn, onResetStatus})(Login)