import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';
import moment from 'moment';

import {isLoading} from 'store/selectors';
import {GET_SUBSCRIPTION_ADD_ONS,} from "constants/ActionTypes";
import {getSubscriptionAddOns} from 'actions/Subscription';
import {onResetStatus} from 'actions/Common';

import SubscriptionStatusTag from "components/Tags/SubscriptionStatus";
import AddOnForm from '../Form/AddOn';

import {
  PlusOutlined,
  EditOutlined,
  ArrowRightOutlined,
} from '@ant-design/icons';
import {
  Spin,
  Table,
  Button,
  Space,
  Tooltip,
  Typography,
} from 'antd';

class SubscriptionAddOn extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      show: null,
      row: null
    }
  }

  componentDidMount() {
    this.props.getSubscriptionAddOns(this.props.subs)
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if ((!prevProps.created && this.props.created)) {
      this.onHideForm();
      this.props.getSubscriptionAddOns(this.props.subs);
      this.props.onResetStatus();
    }
    if ((!prevProps.edited && this.props.edited)) {
      this.onHideForm();
      this.props.getSubscriptionAddOns(this.props.subs);
      this.props.onResetStatus();
    }
  }

  onShowForm = (row = null) => this.setState({show: true, row});

  onHideForm = () => this.setState({show: null, row: null});

  render() {

    const {loading, subscription, addOns} = this.props;

    const column = [
      {
        title: 'Status',
        dataIndex: 'status',
        key: 'status',
        render: (status) => (status && <SubscriptionStatusTag status={status}/>),
        sorter: (a, b) => {
          const sorted = ['active', 'incoming', 'expired'];
          return sorted.indexOf(a.status) - sorted.indexOf(b.status)
        },
        defaultSortOrder: 'ascend'
      },
      {
        title: 'Limit',
        dataIndex: 'limit',
        key: 'limit',
        render: ({code, reset}) => (<div>
          <Typography.Text strong>{_.capitalize(code)}</Typography.Text><br/>
          <Typography.Text type={'secondary'}>{reset}</Typography.Text>
        </div>)
      },
      {
        title: 'Start Date',
        dataIndex: 'start_date',
        key: 'start_date',
        render: (date) => date ? moment(date).format('LL') : null,
        sorter: (a, b) => a.start_date - b.start_date
      },
      {
        title: 'End Date',
        dataIndex: 'end_date',
        key: 'end_date',
        render: (date) => date ? moment(date).format('LL') : null,
        sorter: (a, b) => a.end_date - b.end_date
      },
      {
        title: 'Amount changes',
        key: 'amount_changes',
        render: (text, {amount, is_unlimited: addOnUnlimited, limit: {limit, is_unlimited: limitUnlimited}}) => {
          const renderAmount = ({limit, limitUnlimited, amount, addOnUnlimited}) => {
            if (limitUnlimited || addOnUnlimited)
              return <Typography.Text type={'success'} strong>Unlimited</Typography.Text>;
            else if (_.isNull(limit) && amount)
              return amount;
            else if (_.isNull(limit))
              return <Typography.Text type={'danger'} strong>Locked</Typography.Text>;
            else if (amount)
              return limit + amount;
            else
              return limit
          };
          return <>{renderAmount({limit, limitUnlimited})} <ArrowRightOutlined/> {renderAmount({
            amount,
            limit,
            addOnUnlimited
          })}</>
        },
      },
      {
        title: 'Action',
        key: 'action',
        render: (text, record) => (
          <Space>
            <Tooltip title={'Edit'}>
              <Button type="link" icon={<EditOutlined/>} disabled={subscription.status !== 'active'}
                      onClick={() => this.onShowForm({subscription, ...record})}/>
            </Tooltip>
          </Space>
        ),
      },
    ];

    return (<Spin spinning={loading}>
      <Space size={'middle'} direction={'vertical'} style={{width: '100%'}}>
        <Button type="primary" style={{float: 'right'}} icon={<PlusOutlined/>}
                disabled={subscription.status !== 'active'}
                onClick={() => this.onShowForm({subscription})}>
          Add-ons
        </Button>
        <Table columns={column} dataSource={addOns} rowKey={'id'} style={{width: '100%'}}/>
      </Space>
      <AddOnForm show={this.state.show} onCancel={this.onHideForm} row={this.state.row}/>
    </Spin>);
  }

}

const mapStateToProps = (store) => {
  const {subscription: {subscription, addOns}, addOn: {status: {created, edited}}} = store;
  return {
    loading: isLoading(store, GET_SUBSCRIPTION_ADD_ONS),
    subscription, addOns, created, edited
  }
};

export default connect(mapStateToProps, {
  getSubscriptionAddOns, onResetStatus
})(SubscriptionAddOn);