import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';
import moment from "moment";
import './Quota.css'

import {isLoading} from 'store/selectors';
import {GET_SUBSCRIPTION_QUOTA} from "constants/ActionTypes";
import {getSubscriptionQuota} from 'actions/Subscription';

import {TagsOutlined} from '@ant-design/icons';
import { orange, red, green } from '@ant-design/colors';

import {
  Collapse, Space, Typography, Progress, Row, Col, Statistic, Divider, Spin, Select
} from 'antd';

import UsageChart from "../Chart/UsageChart";

class SubscriptionQuota extends React.Component {

  componentDidMount() {
    this.props.getSubscriptionQuota(this.props.subs)
  }

  handleDateChange = value => {
    const [start, end] = _.split(value, ';');
    this.props.getSubscriptionQuota(this.props.subs, {start, end});
  };

  renderLimit = (is_unlimited, limit, reset, addOns) => {
    if (is_unlimited)
      return <Statistic title="Limit" value={'∞'} suffix={addOns.length ? <TagsOutlined/> : null} valueStyle={{color: green.primary}}/>;
    else if (_.isNull(limit))
      return <Statistic title="Limit" value={'Locked'} valueStyle={{color: red.primary}}/>;
    else
      return <>
        <Statistic title="Limit" value={limit} suffix={addOns.length ? <TagsOutlined/> : null}/>
        {reset && reset !== 'never' && <Typography.Text type={'secondary'}>{reset}</Typography.Text>}
      </>;
  };

  renderMiniProgress = (is_unlimited, limit, usage) => {
    if (is_unlimited)
      return <Space>
        <Typography.Text strong type={'success'}>Unlimited</Typography.Text>
        <div style={{width: 170}}><Progress percent={100}/></div>
      </Space>;
    else if (_.isNull(limit))
      return <Space>
        <Typography.Text strong type={'danger'}>Locked</Typography.Text>
        <div style={{width: 170}}><Progress percent={100} status="exception"/></div>
      </Space>;
    else {
      const percent = usage / limit * 100;
      if (percent >= 100)
        return <Space>
          <Typography.Text strong type={'danger'}>Limit Reached</Typography.Text>
          <div style={{width: 170}}><Progress percent={100} status="exception"/></div>
        </Space>;
      else if (percent >= 95)
        return <div style={{width: 170}}><Progress percent={Math.round(percent)}
                                                   strokeColor={{'0%': red.primary, '100%': red.primary}}/>
        </div>;
      else if (percent >= 80)
        return <div style={{width: 170}}><Progress percent={Math.round(percent)} strokeColor={{
          '0%': orange.primary,
          '100%': orange.primary
        }}/></div>;
      else
        return <div style={{width: 170}}><Progress percent={Math.round(percent)}/></div>
    }
  };

  renderProgress = (is_unlimited, limit, usage) => {
    if (is_unlimited)
      return <Progress type={'circle'} percent={100} format={() => '∞'}/>;
    else if (_.isNull(limit))
      return <Progress type={'circle'} percent={100} status="exception" format={() => 'Locked'}/>;
    else {
      const percent = usage / limit * 100;
      if (percent >= 100)
        return <Progress type={'circle'} percent={100} status="exception" format={() => 'Reached'}/>;
      else if (percent >= 95)
        return <Progress type={'circle'} percent={Math.round(percent)}
                         strokeColor={{'0%': red.primary, '100%': red.primary}}/>;
      else if (percent >= 80)
        return <Progress type={'circle'} percent={Math.round(percent)}
                         strokeColor={{'0%': orange.primary, '100%': orange.primary}}/>;
      else
        return <Progress type={'circle'} percent={Math.round(percent)}/>
    }
  };

  render() {

    const {loading, quota} = this.props;

    const periods = quota.periods ? quota.periods : [];

    const quotas = quota.quotas ? quota.quotas : [];

    const dateStringValue = ([start, end]) => moment(start).format('YYYY-MM-DD') + ';' + moment(end).format('YYYY-MM-DD');

    const current = _.find(periods, ([start, end]) => moment().isBetween(moment(start), moment(end), 'day', '[]')) ?
      _.find(periods, ([start, end]) => moment().isBetween(moment(start), moment(end), 'day', '[]')) :
      _.last(periods);

    return (
      <Spin spinning={loading}>
        <Space direction={'vertical'} style={{width: '100%'}}>
          {periods.length &&
          <Select
            showSearch
            defaultValue={dateStringValue(current)}
            onChange={this.handleDateChange}
            loading={loading} style={{float: 'right'}}>
            {_.map(periods, ([start, end]) => <Select.Option value={dateStringValue([start, end])}>
              {moment(start).format('DD MMMM YYYY')} - {moment(end).format('DD MMMM YYYY')}
            </Select.Option>)}
          </Select>}
          <Collapse
            accordion
            defaultActiveKey={['1']}
            bordered={false}
            className="site-collapse-custom-collapse">
            {_.map(quotas, ({id, code, usage, limit, reset, is_unlimited, chart, addOns}, index) => (
              <Collapse.Panel
                header={<Typography.Text>{_.capitalize(code)}</Typography.Text>}
                extra={this.renderMiniProgress(is_unlimited, limit, _.sumBy(chart, 'usages'))}
                key={(index + 1) + ''}
                className="site-collapse-custom-panel">
                <Row gutter={[16, 24]}>
                  <Col span={6}>
                    <Row gutter={[16, 24]}>
                      <Col span={11}><Statistic title="Usage" value={usage}/></Col>
                      <Col span={2}><Divider type="vertical" style={{height: '100%'}}/></Col>
                      <Col span={11}>{this.renderLimit(is_unlimited, limit, reset, addOns)}</Col>
                      <Col span={24} style={{padding: '45px 8px'}}>
                        <Space direction="horizontal" style={{width: '100%', justifyContent: 'center'}}>
                          {this.renderProgress(is_unlimited, limit, _.sumBy(chart, 'usages'))}
                        </Space>
                      </Col>
                    </Row>
                  </Col>
                  <Col span={18}>
                    <UsageChart identifier={`usage-chart-${index}`} data={chart}/>
                  </Col>
                </Row>
              </Collapse.Panel>
            ))}
          </Collapse>
        </Space>
      </Spin>
    );
  }

}

const mapStateToProps = (store) => {
  const {subscription: {subscription, quota}} = store;
  return {
    loading: isLoading(store, GET_SUBSCRIPTION_QUOTA),
    subscription, quota
  }
};

export default connect(mapStateToProps, {getSubscriptionQuota})(SubscriptionQuota);