import React from 'react';
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom';

import {Layout, Row} from 'antd';

import Menu from './Menu';

import {toggleCollapsedNav} from '../../actions/Setting';

const {Sider} = Layout;



class Sidebar extends React.Component {

  render() {
    const {authUser, navCollapsed, toggleCollapsedNav} = this.props;
    return (
      <Sider
        trigger={null}
        collapsible
        collapsed={navCollapsed}
        onMouseOver={() => toggleCollapsedNav(false)}
        onMouseOut={() => toggleCollapsedNav(true)}
        style={{
          overflow: 'auto',
          height: '100vh',
          position: 'fixed',
          left: 0,
          zIndex: 1
        }}>
        <Row type={'flex'} style={{alignItems: 'center', width: '100%'}}>
          {navCollapsed ?
            <img className={'logo'} src={process.env.PUBLIC_URL + '/my.png'} alt={'icon'}/> :
            <img className={'logo'} src={process.env.PUBLIC_URL + '/logo-mysights-white.png'} alt={'icon'}/>}
        </Row>
        {authUser && <Menu/>}
      </Sider>
    );
  }

}

const mapStateToProps = ({settings, auth}) => {
  const {navCollapsed} = settings;
  const {authUser} = auth;
  return {navCollapsed, authUser}
};

export default withRouter(connect(mapStateToProps, {toggleCollapsedNav})(Sidebar));