import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';

import {isLoading} from 'store/selectors';
import {addService, editService} from 'actions/Service';
import {
  ADD_SERVICE, EDIT_SERVICE
} from "constants/ActionTypes";

import {Icon} from '@ant-design/compatible';
import {QuestionCircleOutlined} from '@ant-design/icons';
import {
  Button,
  Modal,
  Form,
  Input,
  Select,
  Typography,
  Switch,
  Tooltip,
  AutoComplete
} from 'antd';


const layout = {
  labelCol: {span: 8},
  wrapperCol: {span: 16}
};

class Service extends React.Component {

  GATEWAY_URL = 'https://api.mysights.id/api';
  
  BASE_INFO = 'components/Service/';
  
  constructor(props) {
    super(props);
    this.state = {
      formRef: React.createRef(),
      icon: '',
    }
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.show && this.props.show) {
      this.state.formRef.current.resetFields();
      if (this.props.row) {
        const {info, id, ...row} = this.props.row;
        const _info = info ? _.replace(info, this.BASE_INFO, '') : null;
        const init = {is_active: true, is_public: false};
        this.state.formRef.current.setFieldsValue({...init, ...row, info: _info});
      }
    }
  }

  onFinish  = () => {
    const {formRef} = this.state;
    const {row, addService, editService} = this.props;
    formRef.current
      .validateFields()
      .then(({info, ...payload}) => {
        const _info = info ? this.BASE_INFO + info : null;
        if (row.id)
          editService(row.id, {info: _info, ...payload});
        else
          addService({info: _info, ...payload});
      })
      .catch((info) => console.log('Validate Failed:', info));
  };

  onIconSearch = (icon) => {
    this.setState({icon});
  };

  getAvailableIcons = (search = '') => {
    return _(require('@ant-design/icons'))
      .filter((icon) => icon.render && _.includes(icon.render.displayName, 'Outlined'))
      .map(icon => {
        const value = _.kebabCase(_.replace(icon.render.displayName, 'Outlined', ''));
        return {
          label: <><Icon type={value}/> {value}</>,
          value: _.kebabCase(_.replace(icon.render.displayName, 'Outlined', ''))
        }
      })
      .filter(({value}) => _.includes(value, search))
      .value()
  };

  render() {

    const {show, onCancel, row, loading, services} = this.props;

    const {formRef, icon} = this.state;

    return (
      <Modal
        visible={show}
        title="Service"
        width={800}
        onCancel={onCancel}
        footer={[
          <Button key="back" onClick={onCancel}>Cancel</Button>,
          <Button key="submit" type="primary" loading={loading[row && row.id ? 'edit' : 'create']}
                  onClick={this.onFinish}>Submit</Button>,
        ]}>

        <Form {...layout} ref={formRef} name="control-ref" onFinish={this.onFinish}>

          <Form.Item name="service_group_id" label={'Service Group'} rules={[
            {required: true, message: 'Please select the service group!'}
          ]}>
            <Select
              showSearch
              placeholder="Select group"
              optionFilterProp="children"
              filterOption={(input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
              loading={loading.package}
              allowClear>
              {_.map(services, ({id, name}) => <Select.Option value={id}>{name}</Select.Option>)}
            </Select>
          </Form.Item>

          <Form.Item name="name" label="Service Name" rules={[
            {required: true, message: 'Please input group name!'}
          ]}>
            <Input/>
          </Form.Item>

          <Form.Item name="description" label="Description">
            <Input.TextArea/>
          </Form.Item>

          <Form.Item name="icon" label="Icon">
            <AutoComplete options={this.getAvailableIcons(icon)} onSearch={this.onIconSearch}/>
          </Form.Item>

          <Form.Item name="method" label="Method" rules={[
            {required: true, message: 'Please select method!'}
          ]}>
            <Select>
              <Select.Option value="get"><Typography.Text strong type={'success'}>GET</Typography.Text></Select.Option>
              <Select.Option value="post"><Typography.Text strong
                                                           type={'warning'}>POST</Typography.Text></Select.Option>
            </Select>
          </Form.Item>

          <Form.Item name="mask_url" label={
            <span>
            Mask URL&nbsp;
              <Tooltip title="URL accessed by user (published)">
              <QuestionCircleOutlined/>
            </Tooltip>
          </span>
          } rules={[
            {required: true, message: 'Please input mask URL!'}
          ]}>
            <Input prefix={this.GATEWAY_URL}/>
          </Form.Item>

          <Form.Item name="url" label={
            <span>
            Source URL&nbsp;
              <Tooltip title="URL accessed by gateway (protected)">
              <QuestionCircleOutlined/>
            </Tooltip>
          </span>
          } rules={[
            {type: 'url', message: 'The input is not complete URL!'},
            {required: true, message: 'Please input source URL!'}
          ]}>
            <Input/>
          </Form.Item>

          <Form.Item name="info" label="Info Path">
            <Input prefix={this.BASE_INFO}/>
          </Form.Item>

          <Form.Item name="is_active" label="Active" valuePropName="checked">
            <Switch checkedChildren={'Active'} unCheckedChildren={'Inactive'}/>
          </Form.Item>

          <Form.Item name="is_public" label="Public" valuePropName="checked">
            <Switch checkedChildren={'Public'} unCheckedChildren={'Private'}/>
          </Form.Item>

        </Form>

      </Modal>
    );

  }

}

const mapStateToProps = (store) => {
  const {service: {services}} = store;
  return {
    services,
    loading: {
      create: isLoading(store, ADD_SERVICE),
      edit: isLoading(store, EDIT_SERVICE),
    }
  }
};

export default connect(mapStateToProps, {addService, editService})(Service);