import React, {useState} from 'react';

import {Typography, Space} from 'antd';
import {NumberOutlined} from '@ant-design/icons';

const DocsHeader = ({level, text, link}) => {

  const [hover, setHover] = useState(false);

  return (
    <Space onMouseOver={() => setHover(true)}
           onMouseOut={() => setHover(false)}
           style={{width: '100%'}} id={link.replace('#', '')}>
      <Typography.Title level={level} style={{margin: '0.5em 0px'}}>{text}
        {hover && <Typography.Link href={link}> <NumberOutlined/></Typography.Link>}
      </Typography.Title>
    </Space>
  )
};

export default DocsHeader;