import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';
import moment from 'moment';

import {isLoading} from 'store/selectors';
import {
  GET_ALL_SERVICE,
  GET_SUBSCRIPTION,
} from "constants/ActionTypes";
import {getAllService} from 'actions/Service';
import {getSubscriptionByService} from 'actions/Subscription';
import {onResetStatus, onResetDataState} from 'actions/Common';
import SubscriptionStatusTag from "components/Tags/SubscriptionStatus";
import PackageTag from "components/Tags/Package";

import {Icon} from '@ant-design/compatible';
import {PlusOutlined, EditOutlined, EyeOutlined} from '@ant-design/icons';
import {
  Card,
  Menu,
  Layout,
  Empty,
  Spin,
  Table,
  Button,
  Space,
  Tooltip,
  Typography
} from 'antd';

import SubscriptionFrom from './Form/Subs';

class SubscriptionList extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      selected: null,
      show: null,
      row: null
    }
  }

  componentDidMount() {
    this.props.getAllService()
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.created && this.props.created) {
      this.onHideForm();
      this.props.getSubscriptionByService(this.state.selected);
      this.props.onResetStatus();
    }
    if ((!prevProps.edited && this.props.edited)) {
      this.onHideForm();
      this.props.getSubscriptionByService(this.state.selected);
      this.props.onResetStatus();
    }
  }

  componentWillUnmount() {
    this.props.onResetDataState()
  }

  // Menu

  onSelectMenu = ({key}) => {
    // eslint-disable-next-line
    const [item, submenu, id] = _.split(key, '-');
    this.props.getSubscriptionByService(id);
    this.setState({selected: parseInt(id)});
  };

  // Modal

  onShowForm = (kind, row = null) => {
    this.setState({show: kind, row})
  };

  onHideForm = () => {
    this.setState({show: null, row: null})
  };

  render() {

    const {services, loading, subscriptions} = this.props;

    const {selected, show, row} = this.state;

    const column = [
      {
        title: 'Status',
        dataIndex: 'status',
        key: 'status',
        render: (status) => (status && <SubscriptionStatusTag status={status}/>),
        sorter: (a, b) => {
          const sorted = ['active', 'incoming', 'expired', 'inactive'];
          return sorted.indexOf(a.status) - sorted.indexOf(b.status)
        },
        defaultSortOrder: 'ascend'
      },
      {
        title: 'Account',
        dataIndex: 'account',
        key: 'account',
        filters: _(subscriptions).map(({account}) => {
          return {text: account.fullname, value: account.fullname}
        }).uniqBy('value').value(),
        onFilter: (value, {account}) => account.fullname.includes(value),
        render: ({fullname, email}) => (<div>
          <Typography.Text strong>{fullname}</Typography.Text><br/>
          <Typography.Text type={'secondary'}>{email}</Typography.Text>
        </div>)
      },
      {
        title: 'Subs ID',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Package',
        dataIndex: 'package',
        key: 'package',
        render: (_package) => (_package && <PackageTag name={_package.name}/>)
      },
      {
        title: 'Period',
        dataIndex: 'period',
        key: 'period',
        render: (text, {start_date, end_date}) => moment(start_date).format('ll')
          + ' - ' + moment(end_date).format('ll')
      },
      {
        title: 'Recent Usage',
        dataIndex: 'lastUsage',
        key: 'lastUsage',
        render: (usage) => usage ? moment(usage.request_timestamp).fromNow() : '-'
      },
      {
        title: 'Action',
        key: 'action',
        render: (text, {account, ...record}) => (
          <Space>
            <Tooltip title={'Edit'}>
              <Typography.Link onClick={() => this.onShowForm(SubscriptionFrom.type.displayName, {...record, account: account.id})}>
                <EditOutlined/>
              </Typography.Link>
            </Tooltip>
            <Tooltip title={'Detail'}>
              <Typography.Link onClick={() => this.props.history.push(`${this.props.match.url}/${record.id}`)}>
                <EyeOutlined/>
              </Typography.Link>
            </Tooltip>
          </Space>
        ),
      },
    ];

    return (
      <Space direction={'vertical'} style={{width: '100%'}}>
        <Typography.Title level={4}>Subscription</Typography.Title>
        <Card bordered={false}>
          <Spin spinning={loading.service}>
            <Layout style={{minHeight: 600, border: '1px solid #f0f0f0'}}>
              <Layout.Sider className="site-layout-background" width={200}>

                {services.length ?
                  <Menu
                    mode="inline"
                    defaultOpenKeys={['submenu-0']}
                    style={{height: '100%'}}
                    onSelect={this.onSelectMenu}>
                    {_.map(services, ({id, name, icon, services}, index) => {
                      return <Menu.SubMenu key={'submenu-' + index} icon={<Icon type={icon}/>} title={name}>
                        {_.map(services, ({id, name, icon}, i) =>
                          <Menu.Item key={'item-' + index + '-' + id} icon={<Icon type={icon}/>}>{name}</Menu.Item>)}
                      </Menu.SubMenu>
                    })}
                  </Menu>
                  : <Empty/>}

              </Layout.Sider>
              <Layout.Content style={{padding: '0 24px'}}>
                <Spin spinning={loading.subs}>

                  {subscriptions.length ?
                    <Space direction={'vertical'} style={{margin: '24px 0', width: '100%'}}>
                      <Button type="primary"
                              style={{float: 'right'}}
                              icon={<PlusOutlined/>}
                              onClick={() => this.onShowForm(SubscriptionFrom.type.displayName, {service_id: parseInt(selected)})}>
                        Add Subscription
                      </Button>
                      <Table columns={column}
                             dataSource={subscriptions}
                             rowKey={'id'}
                             style={{width: '100%'}}/>
                    </Space>
                    : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE}/>}

                    <SubscriptionFrom show={show === SubscriptionFrom.type.displayName} onCancel={this.onHideForm} row={row}/>

                </Spin>
              </Layout.Content>
            </Layout>
          </Spin>
        </Card>
      </Space>
    );
  }

}

const mapStateToProps = (store) => {
  const {
    service: {services},
    subscription: {subscriptions, status: {created, edited}},
  } = store;
  return {
    loading: {
      service: isLoading(store, GET_ALL_SERVICE),
      subs: isLoading(store, GET_SUBSCRIPTION)
    },
    services, subscriptions,
    created,
    edited
  }
};

export default connect(mapStateToProps, {
  getAllService,
  getSubscriptionByService,
  onResetStatus,
  onResetDataState
})(SubscriptionList);