import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';
import moment from "moment";

import {isLoading} from 'store/selectors';
import {addAddOn, editAddOn} from 'actions/AddOn';
import {getPackage} from 'actions/Package';

import {
  ADD_ADD_ON,
  EDIT_ADD_ON,
} from "constants/ActionTypes";

import {
  Button,
  Modal,
  Form,
  Select,
  DatePicker,
  InputNumber,
  Switch
} from 'antd';

const layout = {
  labelCol: {span: 8},
  wrapperCol: {span: 16}
};

class AddOnForm extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      formRef: React.createRef(),
    }
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.show && this.props.show) {
      this.state.formRef.current.resetFields();
      if (this.props.row) {
        const {subscription, start_date, end_date, ...row} = this.props.row;
        const {package_id} = subscription;

        const init = {is_unlimited: false};

        const date = start_date ? [moment(start_date), moment(end_date)] : null;

        this.state.formRef.current.setFieldsValue({...init, date, ...row});
        this.props.getPackage(package_id);
      }
    }
  }
  
  onFinish  = () => {
    const {formRef} = this.state;
    const {row, addAddOn, editAddOn} = this.props;
    formRef.current
      .validateFields()
      .then(({date, ...payload}) => {
        const [start_date, end_date] = date;
        const {id, subscription, ...rest} = row;
        if (id)
          editAddOn(id, {
            start_date: start_date.format('YYYY-MM-DD'),
            end_date: end_date.format('YYYY-MM-DD'),
            ...payload
          });
        else
          addAddOn({
            ...rest, 
            subscription_id: subscription.id,
            start_date: start_date.format('YYYY-MM-DD'),
            end_date: end_date.format('YYYY-MM-DD'),
            ...payload
          });
      })
      .catch((info) => console.log('Validate Failed:', info));
  };
  
  render() {
    
    const {show, onCancel, row, loading, _package} = this.props;

    const {subscription} = row ? row : {};
    const {end_date} = subscription ? subscription : {};
    const {limits} = _package ? _package : {limits: []};
    
    const {formRef} = this.state;

    const disabledDate = (current) => {
      return end_date && current && 
        (moment(current).isBefore(moment().subtract(1, 'day')) || 
        moment(current).isAfter(moment(end_date).add(1, 'day')))
    };
    
    return(
      <Modal
        visible={show}
        title="Add On"
        onCancel={onCancel}
        footer={[
          <Button key="back" onClick={onCancel}>Cancel</Button>,
          <Button key="submit" type="primary" loading={loading[row && row.id ? 'edit' : 'create']}
                  onClick={this.onFinish}>Submit</Button>,
        ]}>
        
        <Form {...layout} ref={formRef} name="control-ref" onFinish={this.onFinish}>
          
          <Form.Item name="limit_id" label={'Limit'} rules={[{required: true, message: 'Please select the package!'}]}>
            <Select
              showSearch
              placeholder="Select Limit"
              optionFilterProp="children"
              filterOption={(input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
              loading={loading.package}
              allowClear>
              {_.map(limits, ({id, code}) => <Select.Option value={id}>{code}</Select.Option>)}
            </Select>
          </Form.Item>

          <Form.Item name="date" label="Period" rules={[{required: true, message: 'Please input period!'}]}>
            <DatePicker.RangePicker style={{width: '100%'}} disabledDate={disabledDate}/>
          </Form.Item>

          <Form.Item label={'Amount'} name={'amount'} rules={[{required: true, message: 'Please input amount!'}]}>
            <InputNumber min={0}/>
          </Form.Item>

          <Form.Item label={'Unlimited'} valuePropName="checked" name={'is_unlimited'}>
            <Switch checkedChildren={'Unlimited'} unCheckedChildren={'Limited'}/>
          </Form.Item>

        </Form>
        
      </Modal>
    );
    
  }

}

const mapStateToProps = (store) => {
  const {addOn: {status: {created, edited}}, package: {package: _package}} = store;
  return {
    loading: {
      create: isLoading(store, ADD_ADD_ON),
      edit: isLoading(store, EDIT_ADD_ON),
    },
    created, edited, _package
  }
};

export default connect(mapStateToProps, {
  addAddOn,
  editAddOn,
  getPackage
})(AddOnForm);