import {
  SHOW_MESSAGE,
  NOTIFICATION_ERROR,
  GET_SUBSCRIPTION,
  ADD_SUBSCRIPTION,
  UI_START_ACTION,
  UI_STOP_ACTION,
  EDIT_SUBSCRIPTION,
  GET_ONE_SUBSCRIPTION,
  GET_SUBSCRIPTION_SUMMARY,
  GET_SUBSCRIPTION_ADD_ONS,
  GET_SUBSCRIPTION_TIMELINE, GET_SUBSCRIPTION_DAILY_USAGE, GET_SUBSCRIPTION_QUOTA
} from "../constants/ActionTypes";

import axios from 'util/Api'

export const getSubscription = (id) => {
  return dispatch => {
    dispatch({type: UI_START_ACTION, name: GET_ONE_SUBSCRIPTION});
    axios.get('subs/' + id).then(({data}) => {
      dispatch({type: UI_STOP_ACTION, name: GET_ONE_SUBSCRIPTION});
      dispatch({type: GET_ONE_SUBSCRIPTION, payload: data.data});
    }).catch(({response}) => {
      dispatch({type: UI_STOP_ACTION, name: GET_ONE_SUBSCRIPTION});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'Subscription error.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};

