import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';

import {isLoading} from 'store/selectors';
import {
  GET_ALL_SERVICE
} from "constants/ActionTypes";
import {getAllService} from 'actions/Service';
import {onResetStatus, onResetDataState} from 'actions/Common';

import {Icon} from '@ant-design/compatible';
import {PlusOutlined, EditOutlined, InfoCircleOutlined} from '@ant-design/icons';
import {
  Card,
  Menu,
  Layout,
  Empty,
  Spin,
  Button,
  Space,
  Row,
  Col,
  Typography,
  Avatar
} from 'antd';

import GroupForm from './Form/Group';
import ServiceForm from './Form/Service';
import ServiceDetail from './Detail';

class Service extends React.Component {

  ADD_SERVICE_GROUP = 'add-service-group';

  constructor(props) {
    super(props);
    this.state = {
      selected: null,
      show: null,
      row: null
    }
  }

  componentDidMount() {
    this.props.getAllService()
  }

  componentWillUnmount() {
    this.props.onResetDataState()
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.created && this.props.created) {
      this.onHideForm();
      this.props.getAllService();
      this.props.onResetStatus();
    }
    if ((!prevProps.edited && this.props.edited)) {
      this.onHideForm();
      this.props.getAllService();
      this.props.onResetStatus();
    }
  }

  onSelectMenu = ({key}) => {
    if (key !== this.ADD_SERVICE_GROUP) {
      // eslint-disable-next-line
      const [item, index, id] = _.split(key, '-');
      this.setState({selected: parseInt(id)});
    }
  };

  onShowForm = (kind, row = null) => {
    this.setState({show: kind, row})
  };

  onHideForm = () => {
    this.setState({show: null, row: null})
  };

  render() {

    const {services, loading} = this.props;

    const {selected, show, row} = this.state;

    const selectedGroup = selected ? _.find(services, {id: selected}) : null;

    return (
      <Space direction={'vertical'} style={{width: '100%'}}>
        <Typography.Title level={4}>Service Management</Typography.Title>
        <Card bordered={false}>
          <Spin spinning={loading.service}>
            <Layout style={{minHeight: 600, border: '1px solid #f0f0f0'}}>
              <Layout.Sider className="site-layout-background" width={200}>
                {services.length ?
                  <Menu
                    mode="inline"
                    defaultOpenKeys={['submenu-0']}
                    style={{height: '100%'}}
                    onSelect={this.onSelectMenu}>
                    <Menu.Item key={this.ADD_SERVICE_GROUP} icon={<PlusOutlined/>} onClick={() => this.onShowForm(GroupForm.type.displayName)}>
                      Add Service Group
                    </Menu.Item>
                    <Menu.Divider/>
                    {_.map(services, ({id, name, icon}, index) =>
                      <Menu.Item key={'item-' + index + '-' + id} icon={<Icon type={icon}/>}>{name}</Menu.Item>)}
                  </Menu>
                : <Empty/>}
              </Layout.Sider>
              <Layout.Content style={{padding: '0 24px'}}>
                {selectedGroup ?
                <Space direction={'vertical'} style={{margin: '24px 0', width: '100%'}}>
                  <Button type="primary"
                          icon={<EditOutlined/>}
                          onClick={() => this.onShowForm(GroupForm.type.displayName, selectedGroup)}>
                    Edit Group
                  </Button>
                  <Row gutter={[16]}>
                    {_.map(selectedGroup.services, (service) => (
                      <Col {...{xxl: 6, xl: 8, lg: 12, sm: 24}}>
                        <Card className={'service-box'}
                              actions={[
                                <EditOutlined onClick={() => this.onShowForm(ServiceForm.type.displayName, service)}/>,
                                <InfoCircleOutlined
                                  onClick={() => this.onShowForm('detail-service', {service_group: selectedGroup.name, ...service})}/>
                              ]}>
                          <Card.Meta
                            avatar={<Avatar style={{color: '#f56a00', backgroundColor: '#fde3cf'}}
                                            icon={<Icon type={service.icon}/>}/>}
                            title={service.name}
                            description={selectedGroup.name}
                            style={{marginBottom: 16}}
                          />
                          <Typography.Paragraph ellipsis={{rows: 2}}
                                                style={{margin: 0}}>{service.description}</Typography.Paragraph>
                        </Card>
                      </Col>
                    ))}
                    <Col {...{xxl: 6, xl: 8, lg: 12, sm: 24}}>
                      <Button style={{height: '100%', minHeight: 180}} type="dashed" icon={<PlusOutlined/>} block
                              onClick={() => this.onShowForm(ServiceForm.type.displayName, {service_group_id: selectedGroup.id})}>
                        Add Service
                      </Button>
                    </Col>
                  </Row>
                </Space>
                : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE}/>}

                <GroupForm show={show === GroupForm.type.displayName} onCancel={this.onHideForm} row={row}/>

                <ServiceForm show={show === ServiceForm.type.displayName} onCancel={this.onHideForm} row={row}/>

                <ServiceDetail show={show === 'detail-service'} onCancel={this.onHideForm} row={row}/>

              </Layout.Content>
            </Layout>
          </Spin>
        </Card>
      </Space>
    );
  }

}

const mapStateToProps = (store) => {
  const {service: {services, status: {created, edited}}} = store;
  return {
    loading: {
      service: isLoading(store, GET_ALL_SERVICE),
    },
    services, created, edited
  }
};

export default connect(mapStateToProps, {
  getAllService,
  onResetStatus,
  onResetDataState
})(Service);