import React from 'react';

import {Icon} from '@ant-design/compatible';
import {
  Button,
  Modal,
  Descriptions,
  Typography,
  Badge,
  Space
} from 'antd';

const ServiceDetail = (props) => {

  const GATEWAY_URL = 'https://api.mysights.id/api';

  const {show, onCancel, row} = props;

  return (
    <Modal
      visible={show}
      title="Service Info"
      width={800}
      onCancel={onCancel}
      footer={[
        <Button key="back" onClick={onCancel}>Cancel</Button>
      ]}>

      {row &&
      <Descriptions column={1}>
        <Descriptions.Item label="Service Name">{row.name}</Descriptions.Item>
        <Descriptions.Item label="Service Group">{row.service_group}</Descriptions.Item>
        <Descriptions.Item label="Description">{row.description}</Descriptions.Item>
        <Descriptions.Item label="Code">
          <Space>{row.code}<Typography.Text type={'secondary'}>(generated)</Typography.Text></Space>
        </Descriptions.Item>
        <Descriptions.Item label="Icon">
          <Icon type={row.icon}/> {row.icon}
        </Descriptions.Item>
        <Descriptions.Item label="Method">
          {row.method === 'get' && <Typography.Text strong type={'success'}>GET</Typography.Text>}
          {row.method === 'post' && <Typography.Text strong type={'warning'}>POST</Typography.Text>}
        </Descriptions.Item>
        <Descriptions.Item label="Mask URL">
          <Typography.Text code>{GATEWAY_URL + row.mask_url}</Typography.Text>
        </Descriptions.Item>
        <Descriptions.Item label="Source URL">
          <Typography.Text code>{row.url}</Typography.Text>
        </Descriptions.Item>
        <Descriptions.Item label="Info Path">
          <Typography.Text code>{row.info}</Typography.Text>
        </Descriptions.Item>
        <Descriptions.Item label="Active">
          {row.is_active ?
            <Badge status="processing" text="Active"/> :
            <Badge status="default" text="Inactive"/>}
        </Descriptions.Item>
        <Descriptions.Item label="Public">
          {row.is_public ?
            <Badge status="success" text="Public"/> :
            <Badge status="warning" text="Private"/>}
        </Descriptions.Item>
      </Descriptions>}

    </Modal>
  );

};

export default ServiceDetail;