import {
  SHOW_MESSAGE,
  NOTIFICATION_ERROR,

  GET_API_KEY,
  ADD_API_KEY,
  UI_START_ACTION,
  UI_STOP_ACTION,
} from "../constants/ActionTypes";

import axios from 'util/Api'

export const getAPIKey = () => {
  return dispatch => {
    dispatch({type: UI_START_ACTION, name: GET_API_KEY});
    axios.get('api-key').then(({data}) => {
      dispatch({type: UI_STOP_ACTION, name: GET_API_KEY});
      dispatch({type: GET_API_KEY, payload: data.data});
    }).catch(({response}) => {
      dispatch({type: UI_STOP_ACTION, name: GET_API_KEY});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'API key error.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};

export const addAPIKey = () => {
  return dispatch => {
    dispatch({type: UI_START_ACTION, name: ADD_API_KEY});
    axios.post('api-key').then(({data}) => {
      dispatch({type: UI_STOP_ACTION, name: ADD_API_KEY});
      dispatch({type: ADD_API_KEY});
    }).catch(({response}) => {
      dispatch({type: UI_STOP_ACTION, name: ADD_API_KEY});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'API key error.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};