import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';

import {Menu, Row, Col, Popover, Card, Layout} from 'antd';
import {
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  SettingOutlined,
  LogoutOutlined
} from '@ant-design/icons';

import {userSignOut} from 'actions/Auth';
import {toggleCollapsedNav, toggleSetting} from 'actions/Setting';
import UserAvatar from "components/Avatar/index";

import './index.css';

const {Header} = Layout;
const {Meta} = Card;

class Navbar extends React.Component {

  render() {
    const {authUser, navCollapsed, userSignOut, toggleCollapsedNav, toggleSetting} = this.props;

    return (
      <Header className="site-layout-background" style={{padding: 0}}>
        <Row>
          <Col flex="auto">
            {/*{navCollapsed ?*/}
            {/*  <MenuUnfoldOutlined className={'trigger'} onClick={() => toggleCollapsedNav(!navCollapsed)}/> :*/}
            {/*  <MenuFoldOutlined className={'trigger'} onClick={() => toggleCollapsedNav(!navCollapsed)}/>*/}
            {/*}*/}
          </Col>
          <Col>
            <Menu mode={'horizontal'} selectable={false}>
              {authUser && <Menu.Item key={'1'} style={{float: 'right'}}>
                <Popover
                  placement="bottomRight"
                  content={<AvatarContent
                    title={authUser.fullname}
                    description={authUser.email}
                    actions={[
                      <SettingOutlined key="setting" onClick={() => toggleSetting(true)}/>,
                      <LogoutOutlined key="logout" onClick={userSignOut}/>,
                    ]}/>}
                  trigger="hover"
                  overlayStyle={{padding: '0'}}>
                  <Row type={'flex'} style={{alignItems: 'center', margin: '0 5px'}}>
                    <UserAvatar name={authUser.fullname}/>
                  </Row>
                </Popover>
              </Menu.Item>}
            </Menu>
          </Col>
        </Row>
      </Header>
    );
  }

}

const mapStateToProps = ({settings, auth}) => {
  const {navCollapsed} = settings;
  const {authUser} = auth;
  return {navCollapsed, authUser}
};

export default connect(mapStateToProps, {userSignOut, toggleCollapsedNav, toggleSetting})(Navbar);

const AvatarContent = (props) => {
  const {title, description, actions} = props;
  return <Card
    style={{width: 300}}
    cover={
      <img
        alt="example"
        src="https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"
      />
    }
    actions={actions}
  >
    <Meta
      avatar={<UserAvatar name={title} only/>}
      title={_.startCase(title)}
      description={description}
    />
  </Card>
};