import {
  GET_ADD_ON,
  ADD_ADD_ON,
  EDIT_ADD_ON,
  RESET_STATUS,
  RESET_DATA_STATE
} from "../constants/ActionTypes";

const INIT_STATE = {
  addOns: [],
  status: {
    created: null,
    edited: null
  }
};

const AddOn = (state = INIT_STATE, action) => {
  switch (action.type) {
    case GET_ADD_ON:
      return {...state, addOns: action.payload};
    case ADD_ADD_ON:
      return {...state, status: {...state.status, created: true}};
    case EDIT_ADD_ON:
      return {...state, status: {...state.status, edited: true}};
    case RESET_STATUS:
      return {...state, status: INIT_STATE.status};
    case RESET_DATA_STATE:
      return INIT_STATE;
    default:
      return state;
  }
};

export default AddOn;