import React from 'react';
import {Route, Switch, withRouter} from 'react-router-dom';

import Usage from './Usage';
import Service from './Service';

const UsageRoute = ({match}) => (
  <Switch>
    <Route path={`${match.url}/:service`} component={Usage}/>
    <Route path={`${match.url}/`} component={Service}/>
  </Switch>
);

export default withRouter(UsageRoute);