import {
  SHOW_MESSAGE,
  NOTIFICATION_ERROR,
  NOTIFICATION_SUCCESS,

  GET_USER, EDIT_USER, UI_START_ACTION,

  UI_STOP_ACTION,
} from "../constants/ActionTypes";

import axios from 'util/Api'

export const getAllUser = () => {
  return dispatch => {
    dispatch({type: UI_START_ACTION, name: GET_USER});
    axios.get(`user`).then(({data}) => {
      dispatch({type: UI_STOP_ACTION, name: GET_USER});
      dispatch({type: GET_USER, payload: data.data});
    }).catch(({response}) => {
      dispatch({type: UI_STOP_ACTION, name: GET_USER});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'User error.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};

export const editUser = (payload, id = null) => {
  return dispatch => {
    dispatch({type: UI_START_ACTION, name: EDIT_USER});
    axios.post(`user/edit`, payload).then(({data}) => {
      dispatch({type: UI_STOP_ACTION, name: EDIT_USER});
      dispatch({type: EDIT_USER});
    }).catch(({response}) => {
      dispatch({type: UI_STOP_ACTION, name: EDIT_USER});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'User error.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};

export const editPassword = (payload, id = null) => {
  return dispatch => {
    dispatch({type: UI_START_ACTION, name: EDIT_USER});
    axios.post(`user/edit/password`, payload).then(({data}) => {
      dispatch({type: UI_STOP_ACTION, name: EDIT_USER});
      dispatch({type: EDIT_USER});
      dispatch({type: SHOW_MESSAGE, title: 'Password changed.', severity: NOTIFICATION_SUCCESS})
    }).catch(({response}) => {
      dispatch({type: UI_STOP_ACTION, name: EDIT_USER});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'User error.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};