import React, {useState} from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';
import moment from "moment";

import {isLoading} from 'store/selectors';
import {addSubscription, editSubscription} from 'actions/Subscription';
import {getPackageByService} from 'actions/Package';
import {getAllUser} from 'actions/User';
import {
  ADD_SUBSCRIPTION,
  EDIT_SUBSCRIPTION,
  GET_PACKAGE,
  GET_USER
} from "constants/ActionTypes";

import PackageTag from "components/Tags/Package";
import UserAvatar from "components/Avatar";

import {QuestionCircleOutlined} from '@ant-design/icons';
import {
  Button,
  Modal,
  Form,
  Select,
  DatePicker,
  Divider,
  Input,
  Checkbox,
  Tooltip,
  Switch,
  InputNumber
} from 'antd';

const layout = {
  labelCol: {span: 8},
  wrapperCol: {span: 16}
};

const tailLayout = {
  wrapperCol: {offset: 8, span: 16}
};

class SubscriptionForm extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      formRef: React.createRef(),
    }
  }
  
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.show && this.props.show) {
      this.state.formRef.current.resetFields();
      if (this.props.row) {
        const {service_id, start_date, end_date, ...row} = this.props.row;
        const init = {is_active: true, generate_password: true};
        const date = start_date ? [moment(start_date), moment(end_date)] : null;
        this.state.formRef.current.setFieldsValue({...init, date, ...row});
        this.props.getAllUser();
        this.props.getPackageByService(service_id);
      }
    }
  }
  
  onFinish  = () => {
    const {formRef} = this.state;
    const {row, addSubscription, editSubscription} = this.props;
    formRef.current
      .validateFields()
      .then(({account, date, confirmPassword, is_new, generate_password, ...payload}) => {
        const [start_date, end_date] = date;
        if (row.id)
          editSubscription(row.id, {
            ...payload,
            account,
            start_date: start_date.format('YYYY-MM-DD'),
            end_date: end_date.format('YYYY-MM-DD'),
          });
        else
          addSubscription({
            ...row,
            account: is_new ? undefined : account,
            start_date: start_date.format('YYYY-MM-DD'),
            end_date: end_date.format('YYYY-MM-DD'),
            ...payload
          });
      })
      .catch((info) => console.log('Validate Failed:', info));
  };
  
  render() {
    
    const {show, onCancel, row, loading, packages, users} = this.props;
    
    const {formRef} = this.state;

    const getFormState = (state = null) => {
      if (row && row.id)
        return state ? state === 'edit' : 'edit';
      else
        return state ? state === 'create' : 'create';
    };
    
    return(
      <Modal
        visible={show}
        title="Subscription"
        onCancel={onCancel}
        footer={[
          <Button key="back" onClick={onCancel}>Cancel</Button>,
          <Button key="submit" type="primary" loading={loading[getFormState()]}
                  onClick={this.onFinish}>Submit</Button>,
        ]}>
        
        <Form {...layout} ref={formRef} name="control-ref" onFinish={this.onFinish}>

          <Form.Item name="package_id"
                     label={
                       <span>Package&nbsp;
                         {getFormState('edit') &&
                         <Tooltip title="Changing package will inactivate old subs and create the new one!">
                           <QuestionCircleOutlined/>
                         </Tooltip>}</span>
                     }
                     rules={[{required: true, message: 'Please select the package!'}]}>
            <Select
              showSearch
              placeholder="Select package"
              optionFilterProp="children"
              filterOption={(input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
              loading={loading.package}
              allowClear>
              {_.map(packages, ({id, name}) => <Select.Option value={id}><PackageTag name={name}/></Select.Option>)}
            </Select>
          </Form.Item>

          <Form.Item name="date" label="Period" rules={[{required: true, message: 'Please input period!'}]}>
            <DatePicker.RangePicker style={{width: '100%'}} disabled={[getFormState('edit'), false]}
                                    disabledDate={(current) => current && current < moment().subtract(1, 'day').endOf('day')}/>
          </Form.Item>

          <Form.Item label={'Cut-off Date'} name={'cut_off_date'}>
            <InputNumber min={1} max={28} style={{width: '100%'}} disabled={getFormState('edit') && row.status !== 'incoming'}/>
          </Form.Item>

          <Form.Item wrapperCol={{span: 24}} style={{marginBottom: 0}} shouldUpdate>

            {({getFieldValue}) => {
              
              const is_new = getFieldValue('is_new');
              
              return (
                <>
                  <Form.Item {...layout} name="account" label="Account"
                             rules={[{required: !is_new, message: 'Please select the account!'}]}
                             extra={getFormState('edit') ? null : null}>
                    <Select
                      showSearch
                      placeholder="Select account"
                      optionFilterProp="children"
                      disabled={is_new || getFormState('edit')}
                      optionLabelProp={'label'}
                      filterOption={(input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                      allowClear>
                      {_.map(users, ({id, fullname}) => <Select.Option value={id} label={fullname}><UserAvatar
                        name={fullname}/></Select.Option>)}
                    </Select>
                  </Form.Item>

                  {getFormState('create') && 
                  <Form.Item {...tailLayout} name="is_new">
                    <IsNewAccountInput/>
                  </Form.Item>}
                </>
              );
            }}
            
          </Form.Item>

          <Form.Item name="is_active" label="Active" valuePropName="checked">
            <Switch checkedChildren={'Active'} unCheckedChildren={'Inactive'}/>
          </Form.Item>
          
          <Form.Item wrapperCol={{span: 24}} style={{marginBottom: 0}} shouldUpdate>
            
            {({getFieldValue}) => {
              
              const is_new = getFieldValue('is_new');
              
              return(
                <>
                  {is_new && <>
                    <Divider/>

                    <Form.Item {...layout} name="fullname" label="Fullname" rules={[
                      {required: true, message: 'Please input fullname!'}
                    ]}>
                      <Input/>
                    </Form.Item>

                    <Form.Item {...layout} name="email" label="Email" rules={[
                      {type: 'email', message: 'The input is not valid email!'},
                      {required: true, message: 'Please input email!'}
                    ]}>
                      <Input/>
                    </Form.Item>

                    <Form.Item {...layout} name="username" label="Username" rules={[
                      {required: true, message: 'Please input username!',},
                      {min: 8, message: 'Username must be at least 8 characters!'}
                    ]}>
                      <Input/>
                    </Form.Item>

                    <Form.Item {...tailLayout} name="generate_password" valuePropName="checked">
                      <Checkbox>
                        Generate Password &nbsp;
                        <Tooltip title="Password verification will be sent by email">
                          <QuestionCircleOutlined/>
                        </Tooltip>
                      </Checkbox>
                    </Form.Item>
                  </>}
                </>
              );
              
            }}
            
          </Form.Item>
          
          <Form.Item wrapperCol={{span: 24}} style={{marginBottom: 0}} shouldUpdate>
            
            {({getFieldValue}) => {
              
              const generate_password = getFieldValue('generate_password');
              const is_new = getFieldValue('is_new');
              
              return(
                <>
                  {is_new && !generate_password && <>
                    <Divider/>

                    <Form.Item {...layout} name="password" label="Password" hasFeedback rules={[
                      {required: true, message: 'Please input password!',},
                      {min: 8, message: 'Password must be at least 8 characters!'}
                    ]}>
                      <Input.Password type="password"/>
                    </Form.Item>

                    <Form.Item {...layout} name="confirmPassword" label="Confirm Password" dependencies={['password']}
                               hasFeedback
                               rules={[
                                 {required: true, message: 'Please input password again!'},
                                 ({getFieldValue}) => ({
                                   validator(rule, value) {
                                     if (!value || getFieldValue('password') === value) {
                                       return Promise.resolve();
                                     }
                                     return Promise.reject('The two passwords that you entered do not match!');
                                   },
                                 }),
                               ]}>
                      <Input.Password type="password" placeholder="Confirm Password"/>
                    </Form.Item>
                  </>}
                </>
              )
              
            }}
            
          </Form.Item>

        </Form>
        
      </Modal>
    );
    
  }

}

const mapStateToProps = (store) => {
  const {subscription: {status: {created, edited}}, package: {packages}, user: {users}} = store;
  return {
    loading: {
      create: isLoading(store, ADD_SUBSCRIPTION),
      edit: isLoading(store, EDIT_SUBSCRIPTION),
      package: isLoading(store, GET_PACKAGE),
      user: isLoading(store, GET_USER)
    },
    packages, users, created, edited
  }
};

export default connect(mapStateToProps, {
  addSubscription,
  editSubscription,
  getPackageByService,
  getAllUser,
})(SubscriptionForm);

const IsNewAccountInput = ({value = {}, onChange}) => {

  const [is_new, setIsNew] = useState(false);

  const triggerChange = (changedValue) => {
    if (onChange)
      onChange(changedValue)
  };

  const onIsNewAccountChange = (e) => {
    const value = !is_new;
    setIsNew(value);
    triggerChange(value);
  };

  return (
    <Button style={{padding: 0}} type="link" onClick={onIsNewAccountChange}>
      {value.is_new || is_new ? 'Use existing' : 'Register new account?'}
    </Button>
  );

};