import React from 'react';
import {connect} from 'react-redux';
import { Row, Col, Card, Tabs } from 'antd';
import Login from './Login';
import Signup from './Signup';


const { TabPane } = Tabs;

class Auth extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      defaultTabs: "1",
      tabs: "1"
    }
  }

  componentDidMount() {
    const {token, history} = this.props;
    if (token) history.push('/');
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    const {token, history} = this.props;
    if (token) history.push('/');
  }

  onChangeTab = (tab) => this.setState({tabs: tab});

  render() {
    const {defaultTabs, tabs} = this.state;

    return (
      <Row type="flex" justify="center" align="middle" style={{minHeight: '100vh', backgroundImage: `url(/background.png)`}}>
        <Col>
          <Card style={{width: 400}}>
            <Row type="flex" justify="center">
              <Col><img src={'/logo-mysights.png'} alt={'logo mysights'} style={{maxWidth: '150px', marginBottom: '10px'}}/></Col>
            </Row>
            <Tabs defaultActiveKey={defaultTabs} activeKey={tabs} onChange={this.onChangeTab} centered>
              <TabPane tab="LOG IN" key="1">
                <Login/>
              </TabPane>
              <TabPane tab="SIGN UP" key="2">
                <Signup onSuccess={() => this.onChangeTab('1')}/>
              </TabPane>
            </Tabs>
          </Card>
        </Col>
      </Row>
    )
  }
}

const mapStateToProps = ({auth}) => {
  const {token} = auth;
  return {token}
};

export default connect(mapStateToProps, {})(Auth)