import React from 'react';
import {connect} from 'react-redux';

import {UserOutlined, LockOutlined} from '@ant-design/icons';
import {
  Modal, Layout, Menu
} from 'antd';

import {toggleSetting} from 'actions/Setting';

import Profile from "./Profile";
import Security from "./Security";

class Setting extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      selectedMenu: 'profile',
    }
  }

  onSelectMenu = ({key}) => this.setState({selectedMenu: key});

  render() {
    const {openSetting, toggleSetting} = this.props;
    const {selectedMenu} = this.state;
    
    return (
      <Modal 
        visible={openSetting} 
        title="Setting"
        onCancel={() => toggleSetting(false)} width={800}
        bodyStyle={{padding: 0}}
        footer={null}>
        
        <Layout>
          <Layout.Sider className="site-layout-background" width={200}>
            <Menu
              mode="inline"
              defaultSelectedKeys={[selectedMenu]}
              onSelect={this.onSelectMenu}
              style={{height: '100%'}}>
              <Menu.Item key={'profile'} icon={<UserOutlined/>}>Profile</Menu.Item>
              <Menu.Item key={'security'} icon={<LockOutlined/>}>Security</Menu.Item>
            </Menu>
          </Layout.Sider>
          <Layout.Content style={{margin: '16px'}}>
            <MenuContent value={selectedMenu} menu={'profile'} component={<Profile/>}/>
            <MenuContent value={selectedMenu} menu={'security'} component={<Security/>}/>
          </Layout.Content>
        </Layout>
        
      </Modal>
    );
  }

}

const mapStateToProps = ({settings}) => {
  const {openSetting} = settings;
  return {openSetting}
};

export default connect(mapStateToProps, {toggleSetting})(Setting);

const MenuContent = ({menu, value, component}) =>
  menu === value ? component : null;