import {INIT_URL, LOG_OUT, USER_DATA, USER_TOKEN_SET, LOG_IN, SIGN_UP, FORGOT, RESET_STATUS} from "../constants/ActionTypes";

const INIT_STATE = {
  token: localStorage.getItem('token'),
  initURL: '',
  authUser: null,
  menu: [],
  status: {
    signedUp: null,
    loggedIn: null,
    forgot: null
  }
};

const Auth = (state = INIT_STATE, action) => {
  switch (action.type) {
    case INIT_URL: {
      return {...state, initURL: action.payload};
    }
    case SIGN_UP: {
      return {...state, status: {...state.status, signedUp: true}}
    }
    case LOG_IN: {
      return {...state, status: {...state.status, loggedIn: true}}
    }
    case FORGOT: {
      return {...state, status: {...state.status, forgot: true}}
    }
    case LOG_OUT: {
      return {
        ...state,
        token: null,
        authUser: null,
        menu: [],
        initURL: ''
      }
    }
    case USER_DATA: {
      return {
        ...state,
        authUser: action.payload,
        menu: action.menu
      };
    }
    case USER_TOKEN_SET: {
      return {
        ...state,
        token: action.payload,
      };
    }
    case RESET_STATUS: {
      return {...state, status: INIT_STATE.status}
    }
    default:
      return state;
  }
};

export default Auth;