import {GET_USAGE, RESET_DATA_STATE} from "../constants/ActionTypes";

const INIT_STATE = {
  service: null
};

const Usage = (state = INIT_STATE, action) => {
  switch (action.type) {
    case GET_USAGE:
      return {...state, service: action.payload};
    case RESET_DATA_STATE:
      return INIT_STATE;
    default:
      return state;
  }
};

export default Usage;