import {
  SHOW_MESSAGE,
  NOTIFICATION_SUCCESS,
  NOTIFICATION_ERROR,

  UI_START_ACTION,
  UI_STOP_ACTION,

  INIT_URL,
  SIGN_UP,
  LOG_IN,
  FORGOT,
  LOG_OUT,
  USER_DATA,
  USER_TOKEN_SET,
  GET_SINGLE_USER,
  RESET_STATUS
} from "../constants/ActionTypes";
import axios from 'util/Api'

export const setInitUrl = (url) => {
  return {
    type: INIT_URL,
    payload: url
  };
};

export const userSignUp = ({fullname, username, password, email, ...rest}) => {
  return (dispatch) => {
    dispatch({type: UI_START_ACTION, name: SIGN_UP});
    axios.post('auth/register', {fullname, username, password, email}).then(({data}) => {
      dispatch({type: SIGN_UP});
      dispatch({type: UI_STOP_ACTION, name: SIGN_UP});
      dispatch({type: SHOW_MESSAGE, title: data.message, message: '', severity: NOTIFICATION_SUCCESS})
    }).catch(function ({response}) {
      dispatch({type: UI_STOP_ACTION, name: SIGN_UP});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'Failed sign up.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};

export const userSignIn = ({username, password, ...rest}) => {
  return (dispatch) => {
    dispatch({type: UI_START_ACTION, name: LOG_IN});
    axios.post('auth/login', {username, password}).then(({data}) => {

      axios.interceptors.response.use(response => response, (error) => {
        if (error.response && error.response.status === 401) {
          localStorage.removeItem("token");
          dispatch({type: LOG_OUT});
        }
        return Promise.reject(error);
      });

      localStorage.setItem("token", data.data.access_token);
      dispatch({type: UI_STOP_ACTION, name: LOG_IN});
      dispatch({type: USER_TOKEN_SET, payload: data.data.access_token});

    }).catch(function ({response}) {
      dispatch({type: UI_STOP_ACTION, name: LOG_IN});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'Failed login.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};

export const userForgot = ({username, email}) => {
  return (dispatch) => {
    dispatch({type: UI_START_ACTION, name: FORGOT});
    axios.post('forgot', {username, email}).then(({data}) => {
      dispatch({type: FORGOT});
      dispatch({type: UI_STOP_ACTION, name: FORGOT});
      dispatch({type: SHOW_MESSAGE, title: data.message, message: '', severity: NOTIFICATION_SUCCESS})
    }).catch(function ({response}) {
      dispatch({type: UI_STOP_ACTION, name: FORGOT});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'Failed forgot.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};

export const getUser = () => {
  return (dispatch) => {
    dispatch({type: UI_START_ACTION, name: USER_DATA});
    axios.get('auth/me',).then(({data}) => {
      dispatch({type: UI_STOP_ACTION, name: USER_DATA});
      dispatch({type: USER_DATA, payload: data.data, menu: data.data.role.permissions});
    }).catch(function ({response}) {
      dispatch({type: UI_STOP_ACTION, name: USER_DATA});
      if (response) {
        console.log("Error****:", response.message);
        if (response.status === 401) dispatch({type: LOG_OUT}); // Unauthorized
      }
    });
  }
};

export const getForgotUser = (token) => {
  if (token) {
    return dispatch => {
      dispatch({type: UI_START_ACTION, name: GET_SINGLE_USER});
      axios.get(`forgot`, {params: {token}}).then(({data}) => {
        dispatch({type: UI_STOP_ACTION, name: GET_SINGLE_USER});
        dispatch({type: GET_SINGLE_USER, payload: data.data});
      }).catch(({response}) => {
        dispatch({type: UI_STOP_ACTION, name: GET_SINGLE_USER});
        if (response) {
          console.log("Error****:", response.message);
          dispatch({
            type: SHOW_MESSAGE,
            title: 'User error.',
            message: response.data.message,
            severity: NOTIFICATION_ERROR
          })
        }
      });
    }
  }
};

export const userSignOut = () => {
  return (dispatch) => {
    localStorage.removeItem('token');
    dispatch({type: LOG_OUT});
  }
};

export const onResetStatus = () => {
  return (dispatch) => {
    dispatch({type: RESET_STATUS});
  }
};
