import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';

import {isLoading} from 'store/selectors';
import {addPackage, editPackage} from 'actions/Package';
import {
  ADD_PACKAGE, EDIT_PACKAGE
} from "constants/ActionTypes";

import {QuestionCircleOutlined, PlusOutlined, MinusCircleOutlined} from '@ant-design/icons';
import {
  Button,
  Modal,
  Form,
  Input,
  InputNumber,
  Select,
  Switch,
  Tooltip,
  Divider,
  Row, Col,
  List,
  Typography
} from 'antd';

const layout = {
  labelCol: {span: 8},
  wrapperCol: {span: 16}
};

const layoutBlock = {
  wrapperCol: {span: 24}
};

class PackageForm extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      formRef: React.createRef(),
    }
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.show && this.props.show) {
      this.state.formRef.current.resetFields();
      if (this.props.row) {
        const {limits, ...row} = this.props.row;
        const init = {is_public: false, is_paid: false, is_trial: false};
        const _limits = _(limits).filter('is_used').map(({limit, is_unlimited, ...row}) => {
          const is_allowed = limit || is_unlimited;
          return {is_allowed, limit, is_unlimited, ...row};
        }).value();
        this.state.formRef.current.setFieldsValue({...init, limits: _limits, ...row});
      }
    }
  }

  onFinish  = () => {
    const {formRef} = this.state;
    const {row, addPackage, editPackage} = this.props;
    formRef.current
      .validateFields()
      .then((payload) => {
        if (row.id)
          editPackage(row.id, {...payload});
        else
          addPackage({...payload});
      })
      .catch((info) => console.log('Validate Failed:', info));
  };

  render() {

    const {show, onCancel, row, loading, services} = this.props;

    const {formRef} = this.state;

    return(
      <Modal
        visible={show}
        title="Package"
        width={1000}
        onCancel={onCancel}
        footer={[
          <Button key="back" onClick={onCancel}>Cancel</Button>,
          <Button key="submit" type="primary" loading={loading[row && row.id ? 'edit' : 'create']}
                  onClick={this.onFinish}>Submit</Button>,
        ]}>

        <Form {...layout} ref={formRef} name="control-ref" onFinish={this.onFinish}>

          <Form.Item name="service_id" label={'Service'} rules={[
            {required: true, message: 'Please select the service!'}
          ]}>
            <Select
              showSearch
              placeholder="Select service"
              optionFilterProp="children"
              filterOption={(input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
              allowClear disabled>
              {_.map(services, ({id, name}) => <Select.Option value={id}>{name}</Select.Option>)}
            </Select>
          </Form.Item>

          <Form.Item name="name" label="Package Name" rules={[
            {required: true, message: 'Please input package name!'}
          ]}>
            <Input/>
          </Form.Item>

          <Form.Item name="description" label="Description">
            <Input.TextArea/>
          </Form.Item>

          <Form.Item name="is_public" label={
            <span>
            Public Package&nbsp;
              <Tooltip title="Allowed to show in API Market">
              <QuestionCircleOutlined/>
            </Tooltip>
          </span>
          } valuePropName="checked">
            <Switch checkedChildren={'Public'} unCheckedChildren={'Private'}/>
          </Form.Item>

          <Form.Item name="is_paid" label={
            <span>
            Paid Package&nbsp;
              <Tooltip title="Billing enabled">
              <QuestionCircleOutlined/>
            </Tooltip>
          </span>
          } valuePropName="checked">
            <Switch checkedChildren={'Paid'} unCheckedChildren={'Free'}/>
          </Form.Item>

          <Form.Item name="is_trial" label={
            <span>
            Trial Package&nbsp;
              <Tooltip title="Allows user to self-subscribe in API Market (one-time)">
              <QuestionCircleOutlined/>
            </Tooltip>
          </span>
          } valuePropName="checked">
            <Switch checkedChildren={'Trial'} unCheckedChildren={'No Trial'}/>
          </Form.Item>

          <Divider orientation="center">
            <Typography.Text type={'danger'}>*</Typography.Text> Limits
          </Divider>

          <Form.List name="limits"
                     rules={[
                       {
                         validator: async (_, names) => {
                           if (!names || names.length < 1) {
                             return Promise.reject(new Error('Please add at least 1 limit'));
                           }
                         },
                       },
                     ]}>

            {(fields, {add, remove}, {errors}) => (
              <>

                {fields.length ?
                <List >
                  {fields.map(field => (
                    <List.Item key={field.key}>
                      <Row style={{width: '100%'}}>
                        <Col span={11}>

                          <Form.Item
                            {...field}
                            label="Service"
                            name={[field.name, 'service_id']}
                            fieldKey={[field.fieldKey, 'service_id']}
                            rules={[{required: true, message: 'Please input service_id!'}]}
                            hidden
                          >
                            <Input/>
                          </Form.Item>

                          <Form.Item
                            {...field}
                            label="Code"
                            name={[field.name, 'code']}
                            fieldKey={[field.fieldKey, 'code']}
                            rules={[{required: true, message: 'Please input limit code!'}]}
                          >
                            <Input/>
                          </Form.Item>

                          <Form.Item
                            {...field}
                            label="Description"
                            name={[field.name, 'description']}
                            fieldKey={[field.fieldKey, 'description']}
                          >
                            <Input.TextArea/>
                          </Form.Item>

                          <Form.Item
                            {...field}
                            label="Unit"
                            name={[field.name, 'unit']}
                            fieldKey={[field.fieldKey, 'unit']}
                          >
                            <Input/>
                          </Form.Item>

                        </Col>
                        <Col span={11}>

                          <Form.Item {...field} label={'Allowed'} valuePropName="checked"
                                     name={[field.name, 'is_allowed']}
                                     fieldKey={[field.fieldKey, 'is_allowed']}>
                            <Switch checkedChildren={'Allowed'} unCheckedChildren={'Locked'}/>
                          </Form.Item>

                          <Form.Item {...layoutBlock} style={{marginBottom: 0}} shouldUpdate>
                            {({getFieldValue}) => {
                              const limit = getFieldValue('limits')[field.name];
                              return limit && limit.is_allowed &&
                                <Form.Item {...field} {...layout} label={'Unlimited'} valuePropName="checked"
                                           name={[field.name, 'is_unlimited']}
                                           fieldKey={[field.fieldKey, 'is_unlimited']}>
                                  <Switch checkedChildren={'Unlimited'} unCheckedChildren={'Limited'}/>
                                </Form.Item>
                            }}
                          </Form.Item>

                          <Form.Item {...layoutBlock} style={{marginBottom: 0}} shouldUpdate>
                            {({getFieldValue}) => {
                              const limit = getFieldValue('limits')[field.name];
                              return limit && limit.is_allowed && !limit.is_unlimited &&
                                <>
                                  <Form.Item {...field} {...layout} label={'Usage Limit'}
                                             name={[field.name, 'limit']} fieldKey={[field.fieldKey, 'limit']}
                                             rules={[{required: true, message: 'Please input usage!'}]}>
                                    <InputNumber min={0}/>
                                  </Form.Item>

                                  <Form.Item {...field} {...layout} label={'Reset'}
                                             name={[field.name, 'reset']} fieldKey={[field.fieldKey, 'reset']}
                                             rules={[{required: true, message: 'Please select reset!'}]}>
                                    <Select>
                                      <Select.Option value="minutely">minutely</Select.Option>
                                      <Select.Option value="hourly">hourly</Select.Option>
                                      <Select.Option value="daily">daily</Select.Option>
                                      <Select.Option value="weekly">weekly</Select.Option>
                                      <Select.Option value="monthly">monthly</Select.Option>
                                      <Select.Option value="never">never</Select.Option>
                                    </Select>
                                  </Form.Item>
                                </>
                            }}
                          </Form.Item>

                        </Col>
                        
                        <Col span={2}>
                          <MinusCircleOutlined onClick={() => remove(field.name)} />
                        </Col>

                      </Row>
                    </List.Item>
                  ))}
                </List> : null}

                <Form.Item {...layoutBlock}>
                  <Button type="dashed" onClick={() => add({service_id: row.service_id})} block icon={<PlusOutlined/>}>
                    Add limit
                  </Button>
                  <Form.ErrorList errors={errors} />
                </Form.Item>

              </>
            )}

          </Form.List>

        </Form>

      </Modal>
    );
  }

}

const mapStateToProps = (store) => {
  const {service: {services: groups}} = store;
  const services = _.flatMap(groups, ({services}) => services);
  return {
    services,
    loading: {
      create: isLoading(store, ADD_PACKAGE),
      edit: isLoading(store, EDIT_PACKAGE),
    }
  }
};

export default connect(mapStateToProps, {addPackage, editPackage})(PackageForm);