import React from 'react';
import _ from 'lodash';

import {Tag} from 'antd';

const PackageTag = ({name}) => {
  switch (_.lowerCase(name)) {
    case 'paid':
      return <Tag color={'geekblue'}>{_.upperCase(name)}</Tag>;
    case 'trial':
      return <Tag color={'orange'}>{_.upperCase(name)}</Tag>;
    case 'development':
      return <Tag color={'blue'}>{_.upperCase(name)}</Tag>;
    default:
      return <Tag color={'purple'}>{_.upperCase(name)}</Tag>;
  }
};

export default PackageTag