import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';
import moment from "moment";

import {isLoading} from 'store/selectors';
import {GET_USAGE} from "constants/ActionTypes";
import UserAvatar from "components/Avatar";

import {getServiceUsage} from 'actions/Usage';
import {onResetDataState} from 'actions/Common';

import {Icon} from '@ant-design/compatible';
import { AppstoreOutlined, ContainerOutlined, InfoCircleOutlined } from '@ant-design/icons';
import {
  Spin,
  Row,
  Col,
  Avatar,
  Table,
  Typography,
  Space,
  Tabs,
  Select,
  Card
} from 'antd';

import SubscriptionStatusTag from "components/Tags/SubscriptionStatus";
import PackageTag from "components/Tags/Package";

import DailyUsage from "./Tabs/DailyUsage";
import Quota from "./Tabs/Quota";

class ServiceUsage extends React.Component {

  constructor (props) {
    super(props);
    this.state = {
      date: [moment().startOf('month'), moment().endOf('month')],
      column: [
        {
          title: 'Package',
          dataIndex: 'package',
          key: 'package',
          render: (_package) => (_package && <PackageTag name={_package.name}/>)
        },
        {
          title: 'Start Date',
          dataIndex: 'start_date',
          key: 'start_date',
          render: (date) => date ? moment(date).format('LL') : null
        },
        {
          title: 'End Date',
          dataIndex: 'end_date',
          key: 'end_date',
          render: (date) => date ? moment(date).format('LL') : null
        },
        {
          title: 'Status',
          dataIndex: 'status',
          key: 'status',
          render: (status) => (status && <SubscriptionStatusTag status={status}/>)
        },
      ]
    }
  }
  
  componentDidMount() {
    const { match : { params } } = this.props;
    if (params.service) {this.props.getServiceUsage(params.service, params.user)}
  }
  
  componentWillUnmount() {this.props.onResetDataState()}

  createDateRangeArray = (start, end) => {
    var days = [];
    var day = start;

    while (moment(day).isSameOrBefore(end, 'month')) {
      days.push([day.startOf('month').toDate(), day.endOf('month').toDate()]);
      day = day.clone().add(1, 'month');
    }

    return days;
  };

  handleDateChange = value => {
    const { match : { params } } = this.props;
    const [start, end] = _.split(value, ';');
    this.setState({date: [moment(start), moment(end)]});
    if (params.service) {this.props.getServiceUsage(params.service, params.user, moment(start), moment(end))}
  };

  render() {

    const {loading, service} = this.props;
    const {date, column} = this.state;

    const dateStringValue = ([start, end]) => moment(start).format('YYYY-MM-DD') + ';' + moment(end).format('YYYY-MM-DD');
    const dateFilter = service && service.subscription ?
      this.createDateRangeArray(moment(service.subscription.start_date), moment()) : [date];

    return (
      <Space direction={'vertical'} size={'middle'} style={{width: '100%'}}>
        <Typography.Title level={4}>Service Usage</Typography.Title>
        <Card bordered={false} 
              title={'Period'}
              bodyStyle={{padding: 0}}
              extra={[
                <Spin spinning={loading}>
                  <Select
                    showSearch
                    defaultValue={dateStringValue(date)}
                    onChange={this.handleDateChange}
                    loading={loading}>
                    {_.map(dateFilter, ([start, end]) => <Select.Option value={dateStringValue([start, end])}>
                      {moment().isBetween(start, end) ? 'This month' : moment(start).format('MMMM YYYY')}
                    </Select.Option>)}
                  </Select>
                </Spin>
              ]}/>
        <Row gutter={[16]}>
          <Col span={6}>
            <Spin spinning={loading}>
              <Card bordered={false} title={'Service Information'}
                    actions={[<Typography.Link href={`/app/api-market/${service ? service.id : ''}`} target={'_blank'}><InfoCircleOutlined/></Typography.Link>]}
                    bodyStyle={{minHeight: 120}}>
                {service &&
                <Card.Meta
                  avatar={<Avatar style={{color: '#f56a00', backgroundColor: '#fde3cf'}}
                                  icon={<Icon type={service.icon}/>}/>}
                  title={service.name}
                  description={service.service_group.name}
                />}
              </Card>
            </Spin>
          </Col>
          <Col span={6}>
            <Card bordered={false} title={'Customer'} bodyStyle={{minHeight: 168}}>
              <Spin spinning={loading}>
                {service &&
                <Card.Meta
                  avatar={<UserAvatar only name={service.user.fullname}/>}
                  title={<>{service.user.fullname}<br/><Typography.Text type={'secondary'}>{service.user.email}</Typography.Text></>}
                  description={'ID : ' + service.user.mysights_id}
                />}
              </Spin>
            </Card>
          </Col>
          <Col span={12}>
            <Card bordered={false} title={'Subscription'}>
              <Spin spinning={loading}>
                {service && 
                <Table columns={column} dataSource={service.subscriptions} pagination={false} size="small"/>}
              </Spin>
            </Card>
          </Col>
        </Row>
        <Card bordered={false}>
          <Spin spinning={loading}>
            {service &&
            <Tabs defaultActiveKey="1">
              <Tabs.TabPane
                tab={<span><AppstoreOutlined/> Quota</span>}
                key="1">
                <Quota period={date}/>
              </Tabs.TabPane>
              <Tabs.TabPane
                tab={<span><ContainerOutlined/> Daily Usage</span>}
                key="2">
                <DailyUsage period={date}/>
              </Tabs.TabPane>
            </Tabs>}
          </Spin>
        </Card>
      </Space>
    );
  }

}

const mapStateToProps = (store) => {
  const {usage: {service}} = store;
  return {
    loading: isLoading(store, GET_USAGE),
    service
  }
};

export default connect(mapStateToProps, {getServiceUsage, onResetDataState})(ServiceUsage);