import React from 'react';
import _ from 'lodash';
import './index.css';

import {Space, Card, Row, Col, Typography, Divider, Tooltip} from 'antd';
import { orange, geekblue, blue, purple } from '@ant-design/colors';
import {CheckOutlined, CloseOutlined, InfoCircleOutlined, WarningOutlined} from '@ant-design/icons';

const PackageCard = ({package: _package, style}) => {

  const getColor = (name) => {
    switch (_.lowerCase(name)) {
      case 'paid':
        return geekblue;
      case 'trial':
        return orange;
      case 'development':
        return blue;
      default:
        return purple;
    }
  };

  const {name, description, limits} = _package;

  const color = getColor(name);

  const renderLimit = ({is_used, is_unlimited, limit, code, unit, description, reset}) => {
    let limitComponent = null;

    if (is_unlimited)
      limitComponent = <Typography.Text>
        <Typography.Text type={'success'} strong><CheckOutlined/> Unlimited </Typography.Text>
        {description}.
      </Typography.Text>;
    else if (_.isNull(limit))
      limitComponent = <Typography.Text>
        <Typography.Text type={'danger'} strong><CloseOutlined/> </Typography.Text>
        {description}.
      </Typography.Text>;
    else 
      limitComponent = <Typography.Text>
        <Typography.Text type={'success'} strong><CheckOutlined/> </Typography.Text>
        {limit} {unit} of {code} {reset}. {description && <Tooltip title={description}><InfoCircleOutlined/></Tooltip>}
      </Typography.Text>;

    if (is_used)
      return limitComponent;
    else
      return <Typography.Text>
        <Typography.Text delete>{limitComponent}</Typography.Text> <Tooltip title={'recently removed'}><Typography.Text type={'warning'}><WarningOutlined/></Typography.Text></Tooltip>
      </Typography.Text>
  };

  return (
    <Space direction={'vertical'} size={'large'} style={{width: '100%', ...style}}>
      <Card className={'price-box'} style={{backgroundColor: color[4], borderRadius: 16}}>
        <Row justify="center">
          <Col span={24}><Typography.Title level={3} className={'white-text'}>{name} Package</Typography.Title></Col>
          <Col span={24}><Typography.Paragraph strong className={'white-text'}>{description}</Typography.Paragraph></Col>
        </Row>
      </Card>
      <Typography.Title level={5} strong style={{color: color[4], margin: 0}}>Includes</Typography.Title>
      <Divider style={{margin: 0, borderTop: `3px solid ${color[1]}`}}/>
      {_.map(limits, renderLimit)}
    </Space>
  );
};

export default PackageCard