import React from 'react';
import {connect} from 'react-redux';
import {Form, Input, Button} from 'antd';
import {UserOutlined, LockOutlined, InboxOutlined, SolutionOutlined } from '@ant-design/icons';

import {isLoading} from '../store/selectors';
import {userSignUp, onResetStatus} from '../actions/Auth';
import {SIGN_UP} from "../constants/ActionTypes";

class Signup extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      formRef: React.createRef()
    };
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.success && this.props.success) {
      this.onReset();
      this.props.onSuccess();
      this.props.onResetStatus();
    }
  }

  onFinish = (values) => this.props.userSignUp(values);

  onReset = () => this.state.formRef.current.resetFields();

  render() {
    const {loading} = this.props;
    const {formRef} = this.state;

    return (
      <Form
        name="login"
        className="login-form"
        onFinish={this.onFinish}
        ref={formRef}
      >

        <Form.Item
          name="fullname"
          rules={[
            {required: true, message: 'Please input your Username!',},
          ]}
        >
          <Input prefix={<SolutionOutlined className="site-form-item-icon"/>} placeholder="Full name"/>
        </Form.Item>

        <Form.Item
          name="email"
          rules={[
            {type: 'email', message: 'The input is not valid E-mail!',},
            {required: true, message: 'Please input your Email!',},
          ]}
        >
          <Input prefix={<InboxOutlined className="site-form-item-icon"/>} placeholder="Email"/>
        </Form.Item>

        <Form.Item
          name="username"
          rules={[
            {required: true, message: 'Please input your Username!',},
            {min: 8, message: 'Username must be at least 8 characters!'}
          ]}
        >
          <Input prefix={<UserOutlined className="site-form-item-icon"/>} placeholder="Username"/>
        </Form.Item>

        <Form.Item
          name="password"
          hasFeedback
          rules={[
            {required: true, message: 'Please input your Password!',},
            {min: 8, message: 'Password must be at least 8 characters!'}
          ]}
        >
          <Input.Password
            prefix={<LockOutlined className="site-form-item-icon"/>}
            type="password"
            placeholder="Password"
          />
        </Form.Item>

        <Form.Item
          name="confirmPassword"
          dependencies={['password']}
          hasFeedback
          rules={[
            {required: true, message: 'Please input your Password again!',},
            ({getFieldValue}) => ({
              validator(rule, value) {
                if (!value || getFieldValue('password') === value) {
                  return Promise.resolve();
                }
                return Promise.reject('The two passwords that you entered do not match!');
              },
            }),
          ]}
        >
          <Input.Password
            prefix={<LockOutlined className="site-form-item-icon"/>}
            type="password"
            placeholder="Confirm Password"
          />
        </Form.Item>

        <Form.Item>
          <Button type="primary" htmlType="submit" className="login-form-button" loading={!!loading} block>
            Sign up
          </Button>
        </Form.Item>

      </Form>
    )
  };

}

const mapStateToProps = (store) => {
  const {auth: {status: {signedUp}}} = store;
  return {loading: isLoading(store, SIGN_UP), success: !!signedUp}
};

export default connect(mapStateToProps, {userSignUp, onResetStatus})(Signup)