import {GET_PACKAGE, GET_ONE_PACKAGE, RESET_DATA_STATE, RESET_STATUS, ADD_PACKAGE, EDIT_PACKAGE} from "../constants/ActionTypes";

const INIT_STATE = {
  packages: [],
  package: null,
  status: {
    created: null,
    edited: null
  }
};

const Subscription = (state = INIT_STATE, action) => {
  switch (action.type) {
    case GET_PACKAGE:
      return {...state, packages: action.payload};
    case GET_ONE_PACKAGE:
      return {...state, package: action.payload};
    case ADD_PACKAGE:
      return {...state, status: {...state.status, created: true}};
    case EDIT_PACKAGE:
      return {...state, status: {...state.status, edited: true}};
    case RESET_STATUS:
      return {...state, status: INIT_STATE.status};
    case RESET_DATA_STATE:
      return INIT_STATE;
    default:
      return state;
  }
};

export default Subscription;