import {
  SHOW_MESSAGE,
  NOTIFICATION_ERROR,
  VERIFY_EMAIL,
  VERIFY_PASSWORD,
  UI_START_ACTION,
  UI_STOP_ACTION,
} from "../constants/ActionTypes";

import axios from 'util/Api'

export const verifyEmail = (token) => {
  return dispatch => {
    if (token) {
      dispatch({type: UI_START_ACTION, name: VERIFY_EMAIL});
      axios.post(`user/verify/email`, {}, {params: {token}}).then(() => {
        dispatch({type: UI_STOP_ACTION, name: VERIFY_EMAIL});
        dispatch({type: VERIFY_EMAIL, success: true});
      }).catch(({response}) => {
        dispatch({type: UI_STOP_ACTION, name: VERIFY_EMAIL});
        if (response) {
          console.log("Error****:", response.message);
          if (response.status === 404)
            dispatch({type: VERIFY_EMAIL, success: false});
          else
            dispatch({
              type: SHOW_MESSAGE,
              title: 'Verification error.',
              message: response.data.message,
              severity: NOTIFICATION_ERROR
            })
        }
      });
    } else {
      dispatch({type: VERIFY_EMAIL, success: false});
    }
  }
};

export const verifyPassword = (token, password) => {
  return dispatch => {
    if (token) {
      dispatch({type: UI_START_ACTION, name: VERIFY_PASSWORD});
      axios.post(`user/verify/password`, {password}, {params: {token}}).then(() => {
        dispatch({type: UI_STOP_ACTION, name: VERIFY_PASSWORD});
        dispatch({type: VERIFY_PASSWORD, success: true});
      }).catch(({response}) => {
        dispatch({type: UI_STOP_ACTION, name: VERIFY_PASSWORD});
        if (response) {
          console.log("Error****:", response.message);
          if (response.status === 404)
            dispatch({type: VERIFY_PASSWORD, success: false});
          else
            dispatch({
              type: SHOW_MESSAGE,
              title: 'Verification error.',
              message: response.data.message,
              severity: NOTIFICATION_ERROR
            })
        }
      });
    } else {
      dispatch({type: VERIFY_PASSWORD, success: false});
    }
  }
};