import React from 'react';
import {connect} from 'react-redux';

import {isLoading} from 'store/selectors';
import {
  GET_ONE_SUBSCRIPTION,
} from "constants/ActionTypes";
import {getSubscription} from 'actions/Subscription';
import {onResetStatus, onResetDataState} from 'actions/Common';

import Summary from './Tab/Summary';
import AddOn from './Tab/AddOn';
import Timeline from './Tab/Timeline';
import DailyUsage from './Tab/DailyUsage';
import Quota from './Tab/Quota';

import { blue } from '@ant-design/colors';
import {
  TagsOutlined,
  FileTextOutlined,
  CalendarOutlined,
  AppstoreOutlined,
  ContainerOutlined
} from '@ant-design/icons';
import {
  Menu,
  Layout,
  Spin,
  Space,
  Typography,
} from 'antd';

class SubscriptionDetail extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      menu: 'summary',
      show: null,
      row: null
    }
  }

  componentDidMount() {
    const { match : { params } } = this.props;
    if (params.subs) {this.props.getSubscription(params.subs)}
  }

  componentWillUnmount() {this.props.onResetDataState()}

  onMenuChange = ({key}) => this.setState({menu: key});

  onShowForm = (kind, row = null) => this.setState({show: kind, row});

  onHideForm = () => this.setState({show: null, row: null});

  renderContent = () => {
    const {id} = this.props.subscription;
    switch (this.state.menu) {
      case "summary":
        return (<Summary subs={id}/>);
      case "add-on":
        return (<AddOn subs={id}/>);
      case "timeline":
        return (<Timeline subs={id}/>);
      case "daily-usage":
        return (<DailyUsage subs={id}/>);
      case "quota":
        return (<Quota subs={id}/>);
      default: return null;
    }
  };

  render() {

    const {loading, subscription} = this.props;

    return(
      <Space direction={'vertical'} style={{width: '100%'}}>
        <Typography.Title level={4}>Subscription Detail</Typography.Title>
        <Spin spinning={loading}>
          {subscription &&
          <Layout className="site-layout-background" style={{ padding: '24px 0', minHeight: 600 }}>

            <Layout.Sider className="site-layout-background" width={200}>
              <Space direction={'vertical'} style={{width: '100%', padding: '0 24px', borderRight: '1px solid #f0f0f0', marginBottom: 8}}>
                <Typography.Title level={5} style={{margin: 0, color: blue.primary}}>{subscription.account.fullname}</Typography.Title>
                <Typography.Text type="secondary">{subscription.account.email}</Typography.Text>
                <Typography.Text>ID : {subscription.code}</Typography.Text>
              </Space>
              <Menu
                mode="inline"
                defaultSelectedKeys={[this.state.menu]}
                onSelect={this.onMenuChange}>
                <Menu.Item key="summary" icon={<FileTextOutlined/>}>Summary</Menu.Item>
                <Menu.Item key="add-on" icon={<TagsOutlined/>}>Add-ons</Menu.Item>
                <Menu.Item key="timeline" icon={<CalendarOutlined/>}>Timeline</Menu.Item>
                <Menu.Item key="quota" icon={<AppstoreOutlined/>}>Quota</Menu.Item>
                <Menu.Item key="daily-usage" icon={<ContainerOutlined/>}>Daily Usage</Menu.Item>
              </Menu>
            </Layout.Sider>

            <Layout.Content style={{padding: '0 24px', minHeight: 280}}>
              {this.renderContent()}
            </Layout.Content>

          </Layout>}
        </Spin>
      </Space>
    );
  }

}

const mapStateToProps = (store) => {
  const {subscription: {subscription},} = store;
  return {
    loading: isLoading(store, GET_ONE_SUBSCRIPTION),
    subscription,
  }
};

export default connect(mapStateToProps, {
  getSubscription, onResetStatus, onResetDataState
})(SubscriptionDetail);