import React from 'react';

import DocsHeader from "../DocsHeader";

import {Image, Typography, Space, Row, Col, Anchor} from 'antd';

import banner from './RollBanner_Introduction.png';

const Overview = () => {

  return (
    <Row wrap={false} gutter={[16]}>
      <Col flex="auto">
        <Space direction={'vertical'} style={{width: '100%'}}>

          <Row wrap={false} gutter={[16]}>
            <Col flex="auto">
              <DocsHeader level={4} text={'Overview'} link={'#overview'}/>
              <Typography.Paragraph>Route Optimizer (Service Optimization Analytic Platform/SOAP) service used to
                optimize the delivery of goods which includes vehicle selection, goods-vehicles mapping, and optimal
                route search to reduce shipping costs. This service is suitable for logistic, wholesale, retail,
                manufacture, and other related service.</Typography.Paragraph>

              <DocsHeader level={4} text={'Features'} link={'#features'}/>
              <ul>
                <li>Route Optimization</li>
                <li>Recurring Routes</li>
                <li>Vehicle Choices</li>
                <li>Vehicle Capacity Optimization</li>
                <li>Customer Time Window</li>
                <li>Customer Loading Time</li>
                <li>Customer Point Duration</li>
              </ul>
            </Col>
            <Col flex="none">
              <Image
                width={300}
                src={banner}
              />
            </Col>
          </Row>

        </Space>
      </Col>
      <Col flex="none">
        <Anchor targetOffset={200}>
          <Anchor.Link href="#overview" title="Overview"/>
          <Anchor.Link href="#features" title="Features"/>
        </Anchor>
      </Col>
    </Row>
  );

};

export default Overview