import {GET_ACCESS_LOG, RESET_DATA_STATE} from "../constants/ActionTypes";

const INIT_STATE = {
  logs: []
};

const Log = (state = INIT_STATE, action) => {
  switch (action.type) {
    case GET_ACCESS_LOG:
      return {...state, logs: action.payload};
    case RESET_DATA_STATE:
      return INIT_STATE;
    default:
      return state;
  }
};

export default Log;