import {
  SHOW_MESSAGE,
  NOTIFICATION_ERROR,
  
  ADD_ADD_ON,
  EDIT_ADD_ON,

  UI_START_ACTION,
  UI_STOP_ACTION,
} from "../constants/ActionTypes";

import axios from 'util/Api'

export const addAddOn = (payload) => {
  return dispatch => {
    dispatch({type: UI_START_ACTION, name: ADD_ADD_ON});
    axios.post(`add-on`, payload).then(({data}) => {
      dispatch({type: UI_STOP_ACTION, name: ADD_ADD_ON});
      dispatch({type: ADD_ADD_ON});
    }).catch(({response}) => {
      dispatch({type: UI_STOP_ACTION, name: ADD_ADD_ON});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'Service error.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};

export const editAddOn = (id, payload) => {
  return dispatch => {
    dispatch({type: UI_START_ACTION, name: EDIT_ADD_ON});
    axios.post(`add-on/${id}`, payload).then(({data}) => {
      dispatch({type: UI_STOP_ACTION, name: EDIT_ADD_ON});
      dispatch({type: EDIT_ADD_ON});
    }).catch(({response}) => {
      dispatch({type: UI_STOP_ACTION, name: EDIT_ADD_ON});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'Service error.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};