import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';
import moment from 'moment';

import {isLoading} from 'store/selectors';
import {
  GET_ALL_SERVICE,
  GET_USER,
  GET_ACCESS_LOG,
} from "constants/ActionTypes";
import {getAllService} from 'actions/Service';
import {getAllUser} from 'actions/User';
import {getAccessLog} from 'actions/Log';
import {onResetDataState} from 'actions/Common';
import UserAvatar from "components/Avatar";
import DocsHeader from "components/Service/DocsHeader";

import { PrismLight as SyntaxHighlighter } from 'react-syntax-highlighter';
import json from 'react-syntax-highlighter/dist/esm/languages/prism/json';
import material from 'react-syntax-highlighter/dist/esm/styles/prism/material-light';

import {Icon} from '@ant-design/compatible';
import {LoginOutlined} from '@ant-design/icons';
import {
  Card,
  Menu,
  Layout,
  Empty,
  Spin,
  Space,
  Typography,
  Select,
  Table,
  Tag,
  Modal,
  Button,
  Row, Col,
  Anchor
} from 'antd';

SyntaxHighlighter.registerLanguage('json', json);

class AccessLog extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      service: null, account: null,
      show: false, row: null
    }
  }

  componentDidMount() {
    this.props.getAllService()
  }

  componentWillUnmount() {
    this.props.onResetDataState()
  }

  // Menu

  onSelectMenu = ({key}) => {
    // eslint-disable-next-line
    const [item, submenu, id] = _.split(key, '-');
    this.props.getAllUser();
    this.setState({service: parseInt(id)});
    if (this.state.account) this.props.getAccessLog(parseInt(id), this.state.account);
  };

  onSelectChange = (account) => {
    this.props.getAccessLog(this.state.service, account);
    this.setState({account: parseInt(account)});
  };

  onShowDetail = (row = null) => {this.setState({show: true, row})};

  onHideForm = () => {this.setState({show: false, row: null})};

  renderJSON = (json) => {return JSON.stringify(JSON.parse(json), null, "\t")};

  render() {

    const {services, loading, logs, users} = this.props;

    const columns = [
      {
        title: 'Status',
        dataIndex: 'status_code',
        key: 'status_code',
        filters: [
          {text: <Tag color="cyan">1xx</Tag>, value: 100},
          {text: <Tag color="green">2xx</Tag>, value: 200},
          {text: <Tag color="geekblue">3xx</Tag>, value: 300},
          {text: <Tag color="volcano">4xx</Tag>, value: 400},
          {text: <Tag color="purple">5xx</Tag>, value: 500},
          {text: '-', value: null}
        ],
        onFilter: (value, {status_code}) => {
          if (value)
            return status_code / value >= 1 && status_code / value < 2
          else
            return value === status_code
        },
        render: status => {
          if (status >= 500)
            return <Tag color="purple">{status}</Tag>;
          else if (status >= 400)
            return <Tag color="volcano">{status}</Tag>;
          else if (status >= 300)
            return <Tag color="geekblue">{status}</Tag>;
          else if (status >= 200)
            return <Tag color="green">{status}</Tag>;
          else if (status >= 100)
            return <Tag color="cyan">{status}</Tag>;
          else
            return '-';
        }
      },
      {
        title: 'Account',
        dataIndex: 'api_key',
        key: 'api_key',
        render: api_key => (<div>
          <Typography.Text strong>{this.state.account ? _.find(users, {id: this.state.account}).fullname : '-'}</Typography.Text><br/>
          <Typography.Text type={'secondary'} style={{ width: 100 }} ellipsis={{tooltip: api_key}}>{api_key}</Typography.Text>
        </div>)
      },
      {
        title: 'Subscription',
        dataIndex: 'usage',
        key: 'usage',
        filters: _(logs).map(({usage}) => {
          if (usage)
            return {text: usage.subscription.code, value: usage.subscription.code};
          else
            return {text: '-', value: null};
        }).uniqBy('value').value(),
        onFilter: (value, {usage}) => {
          if (value)
            return usage && usage.subscription.code.includes(value);
          else
            return usage === value
        },
        render: subs => subs ? subs.subscription.code : '-'
      },
      {
        title: 'IP Address', dataIndex: 'ip', key: 'ip',
      },
      {
        title: 'Agent', dataIndex: 'user_agent', key: 'user_agent',
      },
      {
        title: 'Date',
        dataIndex: 'request_timestamp',
        key: 'request_timestamp',
        sorter: (a, b) => moment(a.request_timestamp) - moment(b.request_timestamp),
        render: time => moment(time).subtract(7, 'hours').format('lll')
      },
      {
        title: 'Timing',
        dataIndex: 'response_timestamp',
        key: 'response_timestamp',
        render: (time, {request_timestamp}) => {
          if (time) {
            const duration = moment.duration(moment(time).diff(request_timestamp));
            const minutes = Math.floor(duration.asMinutes());
            const seconds = duration.asSeconds() - minutes * 60;
            return `${minutes} m ${seconds} s`;
          } else {
            return '-'
          }
        }
      },
      {
        title: 'Req',
        key: 'action',
        render: (text, {usage, ...record}) => (
          <Typography.Link onClick={() => this.onShowDetail(record)}>
            <LoginOutlined/>
          </Typography.Link>
        ),
      },
    ];

    return(
      <Space direction={'vertical'} style={{width: '100%'}}>
        <Typography.Title level={4}>Access Log</Typography.Title>
        <Card bordered={false}>
          <Spin spinning={loading.service}>
            <Layout style={{minHeight: 600, border: '1px solid #f0f0f0'}}>
              <Layout.Sider className="site-layout-background" width={200}>

                {services.length ?
                  <Menu
                    mode="inline"
                    defaultOpenKeys={['submenu-0']}
                    style={{height: '100%'}}
                    onSelect={this.onSelectMenu}>
                    {_.map(services, ({id, name, icon, services}, index) => {
                      return <Menu.SubMenu key={'submenu-' + index} icon={<Icon type={icon}/>} title={name}>
                        {_.map(services, ({id, name, icon}, i) =>
                          <Menu.Item key={'item-' + index + '-' + id} icon={<Icon type={icon}/>}>{name}</Menu.Item>)}
                      </Menu.SubMenu>
                    })}
                  </Menu>
                  : <Empty/>}

              </Layout.Sider>
              <Layout.Content style={{padding: '0 24px'}}>
                <Spin spinning={loading.log}>

                  {this.state.service ?
                    <Space direction={'vertical'} style={{margin: '24px 0', width: '100%'}}>
                      <Select showSearch placeholder="Select account" optionFilterProp="children"
                              onChange={this.onSelectChange} optionLabelProp={'label'}
                              filterOption={(input, option) => option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                              loading={loading.user}
                              style={{width: 200}}>
                        {_.map(users, ({id, fullname}) => <Select.Option value={id} label={fullname}><UserAvatar
                          name={fullname}/></Select.Option>)}
                      </Select>
                      {logs.length ?
                        <Table columns={columns} dataSource={logs} rowKey={'id'} style={{width: '100%'}} size={'small'}
                               scroll={{x: '100%'}}/>
                        : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE}/>}

                    </Space>
                    : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE}/>}

                  <Modal
                    visible={this.state.show}
                    title="Request Log"
                    width={1000}
                    onCancel={this.onHideForm}
                    footer={[<Button key="back" type="primary" onClick={this.onHideForm}>Cancel</Button>]}>

                    <Row gutter={[16]}>
                      <Col flex="auto">
                        <Space direction={'vertical'} style={{width: '100%'}}>
                          <Typography.Title id={'#request-param'} level={5}>Request Param</Typography.Title>
                          <SyntaxHighlighter language="json" style={material}>
                            {this.renderJSON(this.state.row ? this.state.row.request_query : '{}')}
                          </SyntaxHighlighter>
                          <Typography.Title id={'#request-body'} level={5}>Request Body</Typography.Title>
                          <SyntaxHighlighter language="json" style={material}>
                            {this.renderJSON(this.state.row ? this.state.row.request_body : '{}')}
                          </SyntaxHighlighter>
                          <Typography.Title id={'#response-body'} level={5}>Response Body</Typography.Title>
                          <SyntaxHighlighter language="json" style={material}>
                            {this.renderJSON(this.state.row ? this.state.row.response_body : '{}')}
                          </SyntaxHighlighter>
                          <Typography.Title id={'#response-error'} level={5}>Response Error</Typography.Title>
                          <SyntaxHighlighter language="json" style={material}>
                            {this.renderJSON(this.state.row ? this.state.row.response_error : '{}')}
                          </SyntaxHighlighter>
                        </Space>
                      </Col>
                      {/*<Col flex="100px">*/}
                      {/*  <Anchor targetOffset={100}>*/}
                      {/*    <Anchor.Link href="#request-param" title="Request Param"/>*/}
                      {/*    <Anchor.Link href="#request-body" title="Request Body"/>*/}
                      {/*    <Anchor.Link href="#response-body" title="Response Body"/>*/}
                      {/*    <Anchor.Link href="#response-error" title="Response Error"/>*/}
                      {/*  </Anchor>*/}
                      {/*</Col>*/}
                    </Row>

                  </Modal>

                </Spin>
              </Layout.Content>
            </Layout>
          </Spin>
        </Card>
      </Space>
    );
  }

}

const mapStateToProps = (store) => {
  const {service: {services}, log: {logs}, user: {users}} = store;
  return {
    loading: {
      service: isLoading(store, GET_ALL_SERVICE),
      user: isLoading(store, GET_USER),
      log: isLoading(store, GET_ACCESS_LOG)
    },
    services, logs, users
  }
};

export default connect(mapStateToProps, {
  getAllService, getAllUser, getAccessLog, onResetDataState
})(AccessLog);