import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';

import {isLoading} from 'store/selectors';
import {
  GET_ALL_SERVICE,
  GET_PACKAGE,
} from "constants/ActionTypes";
import {getAllService} from 'actions/Service';
import {getPackageByService} from 'actions/Package';
import {onResetStatus, onResetDataState} from 'actions/Common';
import PackageCard from "components/PackageCard";

import {Icon} from '@ant-design/compatible';
import {EditOutlined, PlusOutlined} from '@ant-design/icons';
import {
  Card,
  Menu,
  Layout,
  Empty,
  Spin,
  Space,
  Typography,
  Row,
  Col,
  Button
} from 'antd';

import PackageForm from './Form';

class Package extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      selectedService: null,
      show: null,
      row: null
    }
  }

  componentDidMount() {
    this.props.getAllService()
  }

  componentWillUnmount() {
    this.props.onResetDataState()
  }
  
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.created && this.props.created) {
      this.onHideForm();
      this.props.getPackageByService(this.state.selectedService);
      this.props.onResetStatus();
    }
    if ((!prevProps.edited && this.props.edited)) {
      this.onHideForm();
      this.props.getPackageByService(this.state.selectedService);
      this.props.onResetStatus();
    }
  }
  
  // Menu

  onSelectMenu = ({key}) => {
    // eslint-disable-next-line
    const [item, submenu, id] = _.split(key, '-');
    this.props.getPackageByService(id);
    this.setState({selectedService: id});
  };
  
  onShowForm = (row = null) => {
    this.setState({show: true, row})
  };

  onHideForm = () => {
    this.setState({show: null, row: null})
  };

  render() {
    
    const {services, loading, packages} = this.props;
    
    const {selectedService, show, row} = this.state;

    return (
      <Space direction={'vertical'} style={{width: '100%'}}>
        <Typography.Title level={4}>Service</Typography.Title>
        <Card bordered={false}>
          <Spin spinning={loading.service}>
            <Layout style={{minHeight: 600, border: '1px solid #f0f0f0'}}>
              <Layout.Sider className="site-layout-background" width={200}>

                {services.length ?
                  <Menu
                    mode="inline"
                    defaultOpenKeys={['submenu-0']}
                    style={{height: '100%'}}
                    onSelect={this.onSelectMenu}>
                    {_.map(services, ({id, name, icon, services}, index) => {
                      return <Menu.SubMenu key={'submenu-' + index} icon={<Icon type={icon}/>} title={name}>
                        {_.map(services, ({id, name, icon}, i) =>
                          <Menu.Item key={'item-' + index + '-' + id} icon={<Icon type={icon}/>}>{name}</Menu.Item>)}
                      </Menu.SubMenu>
                    })}
                  </Menu>
                  : <Empty/>}

              </Layout.Sider>
              <Layout.Content style={{padding: '24px'}}>
                <Spin spinning={loading._package}>

                  {packages.length ?
                    <Row gutter={[16]}>
                      {_.map(packages, (_package) =>
                        <Col {...{xxl: 6, xl: 8, lg: 12, sm: 24}}>
                          <Card className={'service-box'}
                                actions={[<EditOutlined onClick={() => this.onShowForm(_package)}/>]}>
                            <PackageCard package={_package}/>
                          </Card>
                        </Col>
                      )}
                      <Col {...{xxl: 6, xl: 8, lg: 12, sm: 24}}>
                        <Button style={{height: '100%', minHeight: 180}} type="dashed" icon={<PlusOutlined/>} block 
                                onClick={() => this.onShowForm({service_id: parseInt(selectedService)})}>
                          Add Package
                        </Button>
                      </Col>
                    </Row>
                    : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE}/>}
                    
                    <PackageForm show={show} onCancel={this.onHideForm} row={row}/>

                </Spin>
              </Layout.Content>
            </Layout>
          </Spin>
        </Card>
      </Space>
    );
  }

}

const mapStateToProps = (store) => {
  const {service: {services}, package: {packages, status: {created, edited}}} = store;
  return {
    loading: {
      service: isLoading(store, GET_ALL_SERVICE),
      _package: isLoading(store, GET_PACKAGE),
    },
    services, packages, created, edited
  }
};

export default connect(mapStateToProps, {
  getAllService,
  getPackageByService,
  onResetStatus,
  onResetDataState
})(Package);