export const addService = (payload) => {
  return dispatch => {
    dispatch({type: UI_START_ACTION, name: ADD_SERVICE});
    axios.post(`service`, payload).then(({data}) => {
      dispatch({type: UI_STOP_ACTION, name: ADD_SERVICE});
      dispatch({type: ADD_SERVICE});
    }).catch(({response}) => {
      dispatch({type: UI_STOP_ACTION, name: ADD_SERVICE});
      if (response) {
        console.log("Error****:", response.message);
        dispatch({type: SHOW_MESSAGE, title: 'Service error.', message: response.data.message, severity: NOTIFICATION_ERROR})
      }
    });
  }
};

