import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';

import {isLoading} from 'store/selectors';
import {addServiceGroup, editServiceGroup} from 'actions/Service';
import {
  ADD_SERVICE_GROUP, EDIT_SERVICE_GROUP
} from "constants/ActionTypes";

import {Icon} from '@ant-design/compatible';
import {
  Button,
  Modal,
  Form,
  Input,
  AutoComplete
} from 'antd';

const layout = {
  labelCol: {span: 8},
  wrapperCol: {span: 16}
};

class Group extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      formRef: React.createRef(),
      icon: '',
    }
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (!prevProps.show && this.props.show) {
      this.state.formRef.current.resetFields();
      if (this.props.row)
        this.state.formRef.current.setFieldsValue(this.props.row);
    }
  }

  onFinish  = () => {
    const {formRef} = this.state;
    const {row, addServiceGroup, editServiceGroup} = this.props;
    formRef.current
      .validateFields()
      .then(payload => {
        if (row)
          editServiceGroup(row.id, payload);
        else
          addServiceGroup(payload);
      })
      .catch((info) => console.log('Validate Failed:', info));
  };
  
  onIconSearch = (icon) => {
    this.setState({icon});
  };

  getAvailableIcons = (search = '') => {
    return _(require('@ant-design/icons'))
      .filter((icon) => icon.render && _.includes(icon.render.displayName, 'Outlined'))
      .map(icon => {
        const value = _.kebabCase(_.replace(icon.render.displayName, 'Outlined', ''));
        return {
          label: <><Icon type={value}/> {value}</>,
          value: _.kebabCase(_.replace(icon.render.displayName, 'Outlined', ''))
        }
      })
      .filter(({value}) => _.includes(value, search))
      .value()
  };
  
  render() {
    
    const {show, onCancel, row, loading} = this.props;
    
    const {formRef, icon} = this.state;
    
    return (
      <Modal
        visible={show}
        title="Service Group"
        onCancel={onCancel}
        footer={[
          <Button key="back" onClick={onCancel}>Cancel</Button>,
          <Button key="submit" type="primary" loading={loading[row ? 'edit' : 'create']}
                  onClick={this.onFinish}>Submit</Button>,
        ]}>
        
        <Form {...layout} ref={formRef} name="control-ref" onFinish={this.onFinish}>

          <Form.Item name="name" label="Group Name" rules={[
            {required: true, message: 'Please input group name!'}
          ]}>
            <Input/>
          </Form.Item>

          <Form.Item name="description" label="Description">
            <Input.TextArea/>
          </Form.Item>

          <Form.Item name="icon" label="Icon">
            <AutoComplete options={this.getAvailableIcons(icon)} onSearch={this.onIconSearch}/>
          </Form.Item>
          
        </Form>
        
      </Modal>
    );
    
  }

}

const mapStateToProps = (store) => {
  return {
    loading: {
      create: isLoading(store, ADD_SERVICE_GROUP),
      edit: isLoading(store, EDIT_SERVICE_GROUP),
    }
  }
};

export default connect(mapStateToProps, {addServiceGroup, editServiceGroup})(Group);