import React from 'react';
import {connect} from 'react-redux';
import _ from 'lodash';
import moment from "moment";

import {
  Table, Space, Typography, Anchor, Button
} from 'antd';

import PackageTag from "components/Tags/Package";

class DailyUsage extends React.Component {

  createDateArray = (start, end) => {
    var days = [];
    var day = start;

    while (day <= end) {
      days.push(day.toDate());
      day = day.clone().add(1, 'day');
    }

    return days;
  };

  render() {
    const {service, period} = this.props;
    const [start, end] = period;

    const dateColumn = [{title: 'Date', dataIndex: 'date', key: 'date', sorter: (a, b) => a.date - b.date,
      defaultSortOrder: 'descend', render: (date) => date ? moment(date).format('LL') : null}];
    const column = _(service.subscriptions).map(({package: _package, ...rest}) => {
      return _.map(_package.limits, ({id, code, limit, is_unlimited}) => {
        const key = `${rest.id}-${id}-${code}`;
        return {
          title: () => <Space>{_.capitalize(code)}<PackageTag name={_package.name}/></Space>,
          dataIndex: key,
          key: key,
          sorter: (a, b) => a[key] - b[key],
          render: value => value ? <Typography.Text mark>{value}</Typography.Text> : value,
          _limit: is_unlimited ? 'unlimited' : limit
        }
      })
    }).flatten().value();

    const data = _.map(this.createDateArray(start, moment().isSameOrAfter(end) ? end : moment()), date => {
      const usage = _.map(column, ({key}) => {
        const [subs_id, limit_id] = _.split(key, '-');
        const subs = _.find(service.subscriptions, {id: parseInt(subs_id)});
        const limit = _.find(subs.package.limits, {id: parseInt(limit_id)});
        const usages = _.filter(limit.usages, ({request_timestamp}) => moment(request_timestamp).isSame(date, 'date'));
        return {key, value: _.sumBy(usages, 'amount')}
      });
      return {date, ..._(usage).keyBy('key').mapValues('value').value()}
    });

    return (
      <Space direction={'vertical'} style={{width: '100%'}}>
        <Anchor affix={false} style={{float: 'right'}}>
          <Anchor.Link href="#summary" title={
            <Button type="primary">Total</Button>
          }/>
        </Anchor>
        <Table columns={[...dateColumn, ...column]} dataSource={data} pagination={false} style={{scrollBehavior: 'smooth'}}
               summary={(data => (
                 <Table.Summary.Row id={'summary'}>
                   <Table.Summary.Cell><Typography.Text strong>Total Usage</Typography.Text></Table.Summary.Cell>
                   {_.map(column, ({dataIndex, _limit}) => {
                     if (_limit === 'unlimited')
                       return (<Table.Summary.Cell>
                         <Typography.Text strong type="success">{_.sumBy(data, dataIndex)} / {_limit}</Typography.Text>
                       </Table.Summary.Cell>);
                     else if (parseInt(_limit))
                       return (<Table.Summary.Cell>
                         <Typography.Text strong>{_.sumBy(data, dataIndex)} / {_limit}</Typography.Text>
                       </Table.Summary.Cell>);
                     else if (_.isNull(_limit))
                       return (<Table.Summary.Cell>
                         <Typography.Text strong type="danger">Locked</Typography.Text>
                       </Table.Summary.Cell>);
                     else
                       return <Table.Summary.Cell/>;
                   })}
                 </Table.Summary.Row>
               ))}/>
      </Space>
    );
  }

}

const mapStateToProps = ({usage}) => {
  const {service} = usage;
  return {service}
};

export default connect(mapStateToProps, {})(DailyUsage);