import {
  GET_SUBSCRIPTION,
  GET_ONE_SUBSCRIPTION,
  GET_SUBSCRIPTION_SUMMARY,
  GET_SUBSCRIPTION_ADD_ONS,
  GET_SUBSCRIPTION_TIMELINE,
  GET_SUBSCRIPTION_DAILY_USAGE,
  ADD_SUBSCRIPTION,
  EDIT_SUBSCRIPTION,
  RESET_STATUS,
  RESET_DATA_STATE, GET_SUBSCRIPTION_QUOTA
} from "../constants/ActionTypes";

const INIT_STATE = {
  subscriptions: [],
  subscription: null,
  summary: null,
  addOns: [],
  timeline: [],
  dailyUsage: {},
  quota: {},
  status: {
    created: null,
    edited: null
  }
};

const Subscription = (state = INIT_STATE, action) => {
  switch (action.type) {
    case GET_SUBSCRIPTION:
      return {...state, subscriptions: action.payload};
    case GET_ONE_SUBSCRIPTION:
      return {...state, subscription: action.payload};
    case GET_SUBSCRIPTION_SUMMARY:
      return {...state, summary: action.payload, subscription: {...action.payload, account: state.subscription.account}};
    case GET_SUBSCRIPTION_ADD_ONS:
      return {...state, addOns: action.payload};
    case GET_SUBSCRIPTION_TIMELINE:
      return {...state, timeline: action.payload};
    case GET_SUBSCRIPTION_DAILY_USAGE:
      return {...state, dailyUsage: action.payload};
    case GET_SUBSCRIPTION_QUOTA:
      return {...state, quota: action.payload};
    case ADD_SUBSCRIPTION:
      return {...state, status: {...state.status, created: true}};
    case EDIT_SUBSCRIPTION:
      return {...state, status: {...state.status, edited: true}};
    case RESET_STATUS:
      return {...state, status: INIT_STATE.status};
    case RESET_DATA_STATE:
      return INIT_STATE;
    default:
      return state;
  }
};

export default Subscription;